export const ScrollOverlay = props => `
  &::before, &::after {
    content: "";
    width: 100%;
    height: 1.6rem;
    display: block;
    position: absolute;
    z-index: 5;
    left: 0;
    right: 0;
  }

  &::before {
    top: 0;
    background: ${props};
    background: -moz-linear-gradient(bottom, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background: -webkit-linear-gradient(bottom, ${props} 0%,rgba(255, 255, 255, 0) 100%);
    background: linear-gradient(to bottom, ${props} 0%,rgba(255, 255, 255, 0) 100%);
    background-image: -moz-linear-gradient(bottom, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background-image: -webkit-linear-gradient(bottom, ${props} 0%,rgba(255, 255, 255, 0) 100%);
    background-image: linear-gradient(to bottom, ${props} 0%,rgba(255, 255, 255, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='${props}', endColorstr='${props}00',GradientType=0 );
  }

  &::after {
    bottom: 0;
    background: ${props};
    background: -moz-linear-gradient(top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background: -webkit-linear-gradient(top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background: linear-gradient(to top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background-image: -moz-linear-gradient(top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background-image: -webkit-linear-gradient(top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    background-image: linear-gradient(to top, ${props} 0%, rgba(255, 255, 255, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='${props}', endColorstr='${props}00',GradientType=0 );
  }
`

