import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useModal, setModal, useSection, setSection, useOutside, setOutside, useCupons, setCupons, useDelivery, setDelivery }) {

  return (
    <ElementContext.Provider value={{ useModal, setModal, useSection, setSection, useOutside, setOutside, useCupons, setCupons, useDelivery, setDelivery }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotModal() {
  const context = useContext(ElementContext)
  const { useModal, setModal } = context;
  return { useModal, setModal };
}

export function SnapshotSection() {
  const context = useContext(ElementContext)
  const { useSection, setSection } = context;
  return { useSection, setSection };
}

export function SnapshotOutside() {
  const context = useContext(ElementContext)
  const { useOutside, setOutside } = context;
  return { useOutside, setOutside };
}

export function SnapshotCupons() {
  const context = useContext(ElementContext)
  const { useCupons, setCupons } = context;
  return { useCupons, setCupons };
}

export function SnapshotDelivery() {
  const context = useContext(ElementContext)
  const { useDelivery, setDelivery } = context;
  return { useDelivery, setDelivery };
}
