import React, { useEffect, useRef } from 'react';

import { SnapshotOrder, SnapshotStatus } from '../contexts/Service';

import ComponentStatusMain from './ComponentStatusMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalStatus() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useStatus, setStatus } = SnapshotStatus();
  const { useOrder } = SnapshotOrder();
  const { id } = useStatus;

  useEffect(() => {
    const { status } = useStatus;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useStatus]);

  useEffect(() => {
    const { status } = useStatus;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setStatus({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useStatus]);

  const cancenlFn = () => setStatus((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} zIndex="6">
      {useOrder[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentStatusMain close={cancenlFn} {...{ id }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalStatus;
