import React from 'react';

import { SnapshotCupons } from '../contexts/Settings';

import ComponentMapCupons from './ComponentMapCupons';
import ComponentModalDeleteCupom from './ComponentModalDeleteCupom';

import { Main } from '../styles/Settings';
import { Small } from '../styles/Cupons';

function ComponentSettingsCupons() {

  const { useCupons } = SnapshotCupons();

  return (
    <>
      {Object.keys(useCupons).length
        ? (<Main>
          {Object.entries(useCupons)
            .map((data) => <ComponentMapCupons key={data[0]} {...{ data }} />)}
        </Main>)
        : <Small>Não há cupons publicado!</Small>}
      <ComponentModalDeleteCupom />
    </>
  )
}

export default ComponentSettingsCupons;
