import React, { useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { SnapshotPost, SnapshotSection } from '../contexts/ProductId';
import ElementProvider from '../contexts/Selector';

import ComponentMapSelector from './ComponentMapSelector';

import { Content, Contently, Main, Footer, Simple, Button } from '../styles/Selector';

function ComponentPluginsSelector() {

  const start_empty = { moment: new Date().toISOString(), list: [{ moment: new Date().toISOString() }] };
  const start_li = { [parseInt(Math.random() * 1000000000, 10)]: start_empty };

  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection();
  const { usePost, setPost } = SnapshotPost();
  const { pick = {} } = usePost;
  const [usePick, setPick] = useState(Object.keys(pick).length ? pick : start_li);

  const organize = (a, b) => (a[1].moment > b[1].moment) ? 1 : ((b[1].moment > a[1].moment) ? -1 : 0);

  const isValidNew = (data) => {
    const [, assoc] = data;
    const { name, list = [] } = assoc;
    return !!(name && list.map((val) => !!val.subname).every(val => !!val));
  };

  const newFn = () => {
    if (Object.entries(usePick).map(isValidNew).includes(false)) {
      toast.info('Termine o item atual', Bracael.toast_options);
    }
    else {
      toast('Seletor adicionado!');
      const assoc = {...usePick};
      assoc[parseInt(Math.random() * 1000000000, 10)] = start_empty;
      setPick(assoc);
    };
  };

  const saveFn = () => {
    if (Object.entries(usePick).map(isValidNew).every(val => !!val)) {
      toast.success('Adicionais atualizados!', Bracael.toast_options);
      setSection(1);
      setPost((prevState) => Object({
        ...prevState,
        pick: usePick
      }));
    }
    else {
      toast.error('Preencha todos os campos', Bracael.toast_options);
    };
  };

  const testStatus = (input, output) => {

    return Object.entries(input).map((data, i) => {
      const [id, assoc] = data;
      const { important = false, limit = 1, list = [{}], name = '' } = output[id] || {};

      const isValidListCache = (data, ii) => {
        const { moment, subname = '' } = list[ii] || {};
        return moment ? (data.subname || '') === subname : false;
      };

      const isValidList = (data, ii) => {
        const { moment, subname = '' } = (assoc.list || [{}])[ii] || {};
        return moment ? (data.subname || '') === subname : false;
      };

      return ((assoc.important || false) === important)
      && ((assoc.limit || 1) === limit)
      && ((assoc.name || '') === name)
      && ((assoc.list || [{}]).length < list.length
      ? list.map(isValidList).every((val) => !!val)
      : (assoc.list || [{}]).map(isValidListCache).every((val) => !!val));
    }).includes(false)
  }

  const isValid = () => {

    return Object.keys(usePick).length < Object.keys(pick).length
    ? testStatus(pick, usePick)
    : testStatus(usePick, pick);
  };

  return (
    <Content>
      <ElementProvider {...{ usePick, setPick }}>
        <Contently>
          <Main>
            {Object.entries(usePick)
            .sort(organize)
            .map((data, i) => <ComponentMapSelector key={data[0] || i} {...{ data, i }} />)}
          </Main>
          <Footer>
            <Simple type="button" onClick={() => newFn()}>Novo seletor</Simple>
            <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Confirmar</Button>
          </Footer>
        </Contently>
      </ElementProvider>
    </Content>
  )
}

export default ComponentPluginsSelector;
