import React from 'react';

import { SnapshotSection } from '../contexts/ProductId';

import ComponentPluginsHome from './ComponentPluginsHome';
import ComponentPluginsAdditional from './ComponentPluginsAdditional';
import ComponentPluginsSelector from './ComponentPluginsSelector';
import ComponentErrorSmall from './ComponentErrorSmall';

function ComponentPluginsSection() {

  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection();

  if (useSection === 1) return <ComponentPluginsHome />
  if (useSection === 2) return <ComponentPluginsAdditional />
  if (useSection === 3) return <ComponentPluginsSelector />
  return <ComponentErrorSmall />;
}

export default ComponentPluginsSection;
