import React from 'react';

import { SnapshotSection } from '../contexts/Settings';

import ComponentSettingsHome from './ComponentSettingsHome';
import ComponentSettingsBasic from './ComponentSettingsBasic';
import ComponentSettingsPayment from './ComponentSettingsPayment';
import ComponentSettingsFrete from './ComponentSettingsFrete';
import ComponentSettingsLive from './ComponentSettingsLive';
import ComponentSettingsContainer from './ComponentSettingsContainer';
import ComponentErrorSmall from './ComponentErrorSmall';
import ComponentSettingsAccessibility from './ComponentSettingsAccessibility';
import ComponentSettingsCupom from './ComponentSettingsCupom';
import ComponentSettingsCupons from './ComponentSettingsCupons';

function ComponentSettingsSection() {

  const { useSection } = SnapshotSection();

  if (useSection === -1) return <ComponentSettingsHome />
  if (useSection === 1) return <ComponentSettingsBasic />
  if (useSection === 2) return <ComponentSettingsPayment />
  if (useSection === 3) return <ComponentSettingsFrete />
  if (useSection === 4) return <ComponentSettingsLive />
  if (useSection === 5) return <ComponentSettingsContainer />
  if (useSection === 6) return <ComponentSettingsAccessibility />
  if (useSection === 7) return <ComponentSettingsCupom />
  if (useSection === 8) return <ComponentSettingsCupons />
  return <ComponentErrorSmall />;
}

export default ComponentSettingsSection;
