import React, { useRef, useState } from 'react';

import ElementProvider from '../contexts/Gallery';

import ComponentGalleryBreadcrumb from './ComponentGalleryBreadcrumb';
import ComponentGalleryUpload from './ComponentGalleryUpload';
import ComponentAwaitImages from './ComponentAwaitImages';
import ComponentAwaitDelete from './ComponentAwaitDelete';
import ComponentModalDeleteIMG from './ComponentModalDeleteIMG';
import ComponentModalAddIMG from './ComponentModalAddIMG';

import { Content, Head, Main, Radio } from '../styles/Gallery';

function ComponentProductGallery() {

  const clickRef = useRef(true); // segura segundos clicks
  const [useSection, setSection] = useState(1); // telas entre subir imagem ou usar imagem existente
  const [useDelete, setDelete] = useState({}); // modal de deletar imagem
  const [useAdd, setAdd] = useState({}); // modal de adicionar imagem
  const [useSwitch, setSwitch] = useState(true); // troca entre as telas de adicionar e remover da galeria
  const [useAwait, setAwait] = useState({}); // entra em modo de deletar imagem

  const selectFn = (val) => {
    if (useSection !== val && !useDelete.status) {
      if (val === 1) {
        setSection(val);
        setSwitch(true);
      }
      else {
        setSection(val);
      };
    };
  };

  return (
    <Content>
      <ElementProvider {...{ useSection, setSection, useDelete, setDelete, useAdd, setAdd, useSwitch, setSwitch, useAwait, setAwait, clickRef }}>
        <ComponentGalleryBreadcrumb />
        <Head>
          <ul>
            <Radio status={useDelete.status}>
              <input type="radio" readOnly checked={useSection === 1} />
              <label onClick={() => selectFn(1)}>Subir imagem</label>
            </Radio>
            <Radio status={useDelete.status}>
              <input type="radio" readOnly checked={useSection === 2} />
              <label onClick={() => selectFn(2)}>Imagem existente</label>
            </Radio>
          </ul>
        </Head>
        <Main useSection={useSection}>
        {useAwait.status
          ? <ComponentAwaitDelete />
          : useSection === 1
            ? <ComponentGalleryUpload />
            : <ComponentAwaitImages />}
        </Main>
        <ComponentModalDeleteIMG />
        <ComponentModalAddIMG />
      </ElementProvider>
    </Content>
  );
}

export default ComponentProductGallery;
