import React, { useEffect, useRef } from 'react';

import { SnapshotOverlay } from '../contexts/App';

import { Modal, Close } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentOverlay() {

  const statusRef = useRef(false);
  const modalRef = useRef(null);

  const { useOverlay, setOverlay } = SnapshotOverlay();

  useEffect(() => {
    if (statusRef.current && useOverlay) {
      $(modalRef.current).fadeIn(200);
    };
  }, [statusRef, modalRef, useOverlay]);

  useEffect(() => {
    if (statusRef.current) {
      if (!useOverlay) {
        $(modalRef.current).fadeOut(300);
      };
    }
    else {
      statusRef.current = true;
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useOverlay]);

  return (
    <Modal ref={modalRef} zIndex="4" backgroundColor="rgba(255,255,255,0.6)">
      <Close onClick={() => setOverlay(false)} />
    </Modal>
  );
}

export default ComponentOverlay;
