import React, { useEffect, useRef, useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Form, Section, Input, Footer, Button } from '../styles/Modify';
import { Content, Name, Simple } from '../styles/Frete';

const { jQuery: $ } = window;

function ComponentFreteForm() {

  const inputRef = useRef(null);
  const clickRef = useRef(true);
  const [useZone, setZone] = useState(false)
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { useOutside } = SnapshotOutside();
  const { setModal } = SnapshotModal();
  const [useInput, setInput] = useState({
    moment: new Date().toISOString(),
    district: '',
    rate: 0
  });
  const { district, zone = '', rate } = useInput;
  const [useRate, setRate] = useState(rate ? rate.toFixed(2) : '');
  const { store_zone } = useOutside.profile || {};

  const formatNumber = (val) => isNaN(Number(val))
  ? Number(val.replace(/\./g, '').replace(/,/g, '.'))
  : Number(val);
  const isValid = () => district && rate;
  const inputFn = (val) => {
    setRate(val);
    setInput((prevState) => Object({
      ...prevState,
      rate: formatNumber(val)
    }));
  };

  const labelFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('createDelivery')({ token, data: useInput })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!');
          setRate('');
          setInput({
            moment: new Date().toISOString(),
            district: '',
            rate: 0
          });
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  useEffect(() => {
    inputRef.current.value = useRate;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useRate]);

  useEffect(() => {
    $(inputRef.current).mask('#.##0,00', { reverse: true });
  }, [inputRef]);

  const districtFn = (val) => setInput((prevState) => Object({
    ...prevState,
    district: val
  }));
  const zoneFn = (val) => setInput((prevState) => Object({
    ...prevState,
    zone: val
  }));
  const hiddenFn = () => {
    setZone((prevState) => !prevState);
    if (zone) {
      setInput((prevState) => {
        const assoc = {...prevState};
        delete assoc.zone;
        return assoc;
      });
    };
  };

  return (
    <Content>
      <Name>Adicionar um novo bairro?</Name>
      <Form>
        <Section>
          <Input type="text" placeholder="Bairro" minWidth="72%" maxWidth="100%" onChange={(e) => districtFn(e.target.value)} value={district} />
          <Input type="text" placeholder="Preço" ref={inputRef} maxWidth="100%" onInput={(e) => inputFn(e.target.value)} autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off" />
        </Section>
        {useZone
        ? (<Section>
            <Input type="text" placeholder="Cidade" maxWidth="100%" onChange={(e) => zoneFn(e.target.value)} value={zone} />
          </Section>)
        : null}
      </Form>
      <Footer>
        <Button type="button" status={isValid()} onClick={() => isValid() && labelFn()}>Salvar alterações</Button>
        <Simple type="button" onClick={() => hiddenFn()}>{useZone ? 'Para' : 'Fora de'} {store_zone}?</Simple>
      </Footer>
    </Content>
  );
}

export default ComponentFreteForm;
