import React from 'react';

import { SnapshotStatus } from '../contexts/App';

import { Status } from '../styles/Header';

function ComponentHeaderStatus() {

  // eslint-disable-next-line no-unused-vars
  const { useStatus, setStatus } = SnapshotStatus();

  return (
    <Status status={useStatus}>{useStatus ? 'Delivery aberto!' : 'Delivery fechado!'}</Status>
  );
}

export default ComponentHeaderStatus;
