import React from 'react';
import ReactDOM from 'react-dom';

import { functions, auth } from './services/server';

import App from './App';
import Entrar from './Entrar';

// import './index.css';

async function startDatabase() {
  return await functions().httpsCallable('consoleApp')({
    where: document.querySelector('[name=local]').content
   })
   .then((data) => data.data);
};

const StartConnection = () => Promise.resolve(startDatabase())

auth().onAuthStateChanged(function (user) {
  if (user) {
    StartConnection()
      // .then(function(data){
      //   const { hostname, protocol } = new URL(window.location.href);
      //   // hostname === 'console.bracael.com' && protocol === 'https:' // validar para produção !IMPORTANT
      //   // console.log(hostname === 'localhost' && protocol === 'http:')

      //   return hostname === 'console.bracael.com' && protocol === 'https:' && data !== undefined ? Promise.resolve(data) : Promise.reject()
      // })
      .then(function (data) {
        const { status, ...rest } = data;

        switch(status){
          case 200:
            ReactDOM.render((
              <App {...rest} />
            ), document.getElementById('root'));
            break;
          case 401:
            ReactDOM.render((
              <div>Não autorizado!</div>
            ), document.getElementById('root'));
            break;
          default:
            ReactDOM.render((
              <div>404</div>
            ), document.getElementById('root'));
        }
      })
      .catch(function () {
        ReactDOM.render(<div>ERRO 500</div>, document.getElementById('root'));
      });
  }
  else {
    ReactDOM.render(
      <React.StrictMode>
        <Entrar />
      </React.StrictMode>, document.getElementById('root'));
  }
})
