import React, { useEffect, useRef } from 'react';

import { SnapshotSwipe } from '../contexts/App';

import { Modal, Close } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentSwipe() {

  const statusRef = useRef(false);
  const modalRef = useRef(null);

  const { useSwipe, setSwipe } = SnapshotSwipe();

  useEffect(() => {
    if (statusRef.current && useSwipe) {
      $(modalRef.current).fadeIn(200);
    };
  }, [statusRef, modalRef, useSwipe]);

  useEffect(() => {
    if (statusRef.current) {
      if(!useSwipe) {
        $(modalRef.current).fadeOut(300);
      };
    }
    else {
      statusRef.current = true;
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useSwipe]);

  return (
    <Modal ref={modalRef} zIndex="4" backgroundColor="rgba(0,0,0,0.4)">
      <Close onClick={() => setSwipe(false)} />
    </Modal>
  );
}

export default ComponentSwipe;
