import React, { useRef, useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOrder } from '../contexts/Service';

import { Main, Title, Column, Li, Input, Label, Footer, Simple, Button } from '../styles/Status';

function ComponentStatusMain({ id, close }) {

  const { useOrder } = SnapshotOrder();
  const { status_code = '001' } = useOrder[id];
  const { token } = SnapshotToken();
  const clickRef = useRef(true);
  const { setModal } = SnapshotModal();
  const [useInput, setInput] = useState(status_code);
  const [useAttempts, setAttempts] = useState(0);

  const saveFn = () => {
    if (useInput !== status_code) {
      if(clickRef.current) {
        clickRef.current = false;
        setModal({ status: true });

        functions()
        .httpsCallable('statusOrder')({ token, id, data: useInput })
        .then((data) => data.data)
        .then(function(data) {
          const { status } = data;
          if (!status) {
            if (useAttempts < 3) {
              toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
              setAttempts((prevState) => prevState+1);
            }
            else {
              toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
            };
          };

          close();
          clickRef.current = true;
          setModal({ status: false });
        })
        .catch(function() {
          clickRef.current = true;
          setModal({ status: false });
          toast.error('Erro na chamada API', Bracael.toast_options);
        });
      };
    }
  };

  return (
    <Main>
      <Title>Editar status</Title>
      <Column>
        {Bracael.order_status.map((data) => {
          const { status, name } = data;
          return (
            <Li key={status}>
              <Input type="checkbox" readOnly checked={status === useInput} />
              <Label onClick={() => setInput(status)}>{name}</Label>
            </Li>
          )
        })}
      </Column>
      <Footer>
        <Simple type="button" onClick={() => close()}>Cancelar</Simple>
        <Button type="button" status={useInput !== status_code} onClick={() => saveFn()}>Salvar</Button>
      </Footer>
    </Main>
  )
}

export default ComponentStatusMain;
