import React from 'react';
import { useParams } from 'react-router';
import { useAsync } from "react-async";

import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';

import ComponentProductId from './ComponentProductId';
import ComponentSkeletonProductId from './ComponentSkeletonProductId';

const fetchPerson = async ({ token, id }, { signal }) => await functions()
  .httpsCallable('queryObject')({ token, ref: `feed/${id}` })
  .then((data) => data.data);

function ComponentAwaitProductId() {

  const { token } = SnapshotToken();
  const { id } = useParams();
  const { data, error } = useAsync({ promiseFn: fetchPerson, token, id });

  if (error) return error.message
  if (data) return <ComponentProductId {...{ data }} />
  return <ComponentSkeletonProductId />
};

export default ComponentAwaitProductId;
