import React, { useEffect, useRef } from 'react';

import { SnapshotOrder, SnapshotDecline } from '../contexts/Service';

import ComponentDeclineMain from './ComponentDeclineMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalDecline() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useDecline, setDecline } = SnapshotDecline();
  const { useOrder } = SnapshotOrder();
  const { id } = useDecline;

  useEffect(() => {
    const { status } = useDecline;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useDecline]);

  useEffect(() => {
    const { status } = useDecline;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setDecline({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useDecline]);

  const cancenlFn = () => setDecline((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} zIndex="6">
      {useOrder[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentDeclineMain close={cancenlFn} {...{ id }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalDecline;
