import React, { useEffect } from 'react';
import { useAsync } from "react-async";
import { toast } from 'react-toastify';

import { pathImageUrl } from '../app/';

import Bracael from '../Bracael';
import { storage } from '../services/server';
import { SnapshotWhere } from '../contexts/App';
import { SnapshotAwait } from '../contexts/Gallery';

import ComponentLoading from './ComponentLoading';

const fetchPerson = async ({ useWhere, useAwait }, { signal }) => {

  const images_size = ["90x90", "160x160", "320x320", "640x640", "1024x1024"]
  const { name } = useAwait;

  return await Promise.all(images_size.map(async (size) => {

    return await storage()
    .ref(`${pathImageUrl({ where: useWhere })}/${name}_${size}.jpeg`)
    .delete();
  }))
  .then(async function() {
    toast.info('Arquivo excluído com sucesso', Bracael.toast_options);

    return true;
    // File deleted successfully
  })
  .catch(function(error) {
    toast.error('Ups, ocorreu um erro!', Bracael.toast_options);

    return true;
    // Uh-oh, an error occurred!
  });
};

function ComponentAwaitDelete() {

  const { useWhere } = SnapshotWhere();
  const { useAwait, setAwait } = SnapshotAwait();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, useAwait });

  useEffect(() => {
    if (data) {
      setAwait({});
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  useEffect(() => {
    if (error) {
      setAwait({});
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [error]);

  return <ComponentLoading />
}

export default ComponentAwaitDelete;
;
