import React, { useEffect, useRef, useState } from 'react';

import ElementProvider from '../contexts/Products';

import ComponentHeader from '../components/ComponentHeader';
import ComponentAwaitProducts from '../components/ComponentAwaitProducts';
import ComponentOverload from '../components/ComponentOverload';

import { Mainly } from '../styles/root';

const { jQuery: $ } = window;

function Products() {

  const configGlobal = {
    name_content: "Meus produtos"
  };

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [useModal, setModal] = useState({});
  const [useUpdate, setUpdate] = useState(1);

  useEffect(() => {
    const { status } = useModal;
    if (statusRef.current && status === false) {
      $(modalRef.current).fadeOut(300);
    }
    else {
      statusRef.current = true;
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal]);

  return (
    <>
      <ComponentHeader {...{ configGlobal }} />
      <Mainly>
        <ElementProvider {...{ useModal, setModal, useUpdate, setUpdate }}>
          {(useUpdate & 1)
          ? <ComponentAwaitProducts key={1} />
          : <ComponentAwaitProducts key={2} />}
        </ElementProvider>
        <ComponentOverload {...{ useModal, setModal, modalRef }} />
      </Mainly>
    </>
  );
};

export default Products;
