import React from 'react';
import pt from 'date-fns/locale/pt-BR';
import { format } from 'date-fns';

import Bracael from '../Bracael';

import { Li, Head, Headly, Title, Sub, Button, Main, Shead, Group, Flex, Tlt, Footer, Price, Time } from '../styles/Cupons';

function ComponentMapCupons({ data }) {

  const [id, assoc] = data;
  const { title, type_off = '001', validate, value, max_use, max_value = 0, min_value = 0, moment } = assoc;

  console.log(type_off);

  return (
    <Li>
      <Head>
        <Headly>
          <Title>{id}</Title>
          <Sub>{title}</Sub>
        </Headly>
        <Button type="button">Remover</Button>
      </Head>
      <Main>
        <Shead>
          <Flex>
            <Tlt>Tipo de cupom</Tlt>
            <span>{Bracael.cupom_names[type_off].name}</span>
          </Flex>
          <Flex>
            <Tlt>Cupom válido até</Tlt>
            <span>{validate
            ? format(new Date(validate), "dd/MM/yyyy", { locale: pt })
            : '--'}</span>
          </Flex>
        </Shead>
        <Group>
          <Flex>
            <Tlt>Max. de uso</Tlt>
            <span>{max_use || 'Ilimitado'}</span>
          </Flex>
          <Flex>
            <Tlt>Gasto máx.</Tlt>
            <span>{Number(max_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Flex>
          <Flex>
            <Tlt>Gasto mín.</Tlt>
            <span>{Number(min_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Flex>
        </Group>
      </Main>
      <Footer>
        <Price>{type_off !== '002'
        ? `${value}%`
        : Number(value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })} OFF</Price>
        <Time>{format(new Date(moment), "dd 'de' MMMM 'de' yyyy", { locale: pt })}</Time>
      </Footer>
    </Li>
  );
}

export default ComponentMapCupons;
