import React, { useState } from 'react';

import ComponentBasicStatus from './ComponentBasicStatus';
import ComponentBasicTitle from './ComponentBasicTitle';
import ComponentBasicTel from './ComponentBasicTel';
import ComponentBasicCNPJ from './ComponentBasicCNPJ';
import ComponentBasicAddress from './ComponentBasicAddress';
import ComponentBasicPrice from './ComponentBasicPrice';

import { Main } from '../styles/Settings';

function ComponentSettingsBasic() {

  const [useShow, setShow] = useState(-1);

  return (
    <Main>
      <ComponentBasicStatus />
      <ComponentBasicTitle {...{ useShow, setShow }} />
      <ComponentBasicTel {...{ useShow, setShow }} />
      <ComponentBasicCNPJ {...{ useShow, setShow }} />
      <ComponentBasicAddress {...{ useShow, setShow }} />
      <ComponentBasicPrice {...{ useShow, setShow }} />
    </Main>
  );
}

export default ComponentSettingsBasic;
