import React from 'react';

import { Column, Remove } from '../styles/Additional';

function ComponentAdditionalRemove({ data, useExtra, setExtra }) {

  const id = data[0];
  const buttonFn = () => {
    const assoc = {...useExtra};
    delete assoc[id];
    setExtra(assoc);
  };

  return (
    <Column width="25%">
      <Remove type="button" onClick={() => buttonFn()}>
        <svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 515.556 515.556"><path d="m64.444 451.111c0 35.526 28.902 64.444 64.444 64.444h257.778c35.542 0 64.444-28.918 64.444-64.444v-322.222h-386.666z"></path><path d="m322.222 32.222v-32.222h-128.889v32.222h-161.111v64.444h451.111v-64.444z"></path></svg>
      </Remove>
    </Column>
  );
}

export default ComponentAdditionalRemove;
