import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useModal, setModal, useUpdate, setUpdate }) {

  return (
    <ElementContext.Provider value={{ useModal, setModal, useUpdate, setUpdate }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotModal() {
  const context = useContext(ElementContext)
  const { useModal, setModal } = context;
  return { useModal, setModal };
}

export function SnapshotUpdate() {
  const context = useContext(ElementContext)
  const { useUpdate, setUpdate } = context;
  return { useUpdate, setUpdate };
}
