import React from 'react';
import { useHistory } from 'react-router-dom';

import { SnapshotBar } from '../contexts/ListProducts';

import ComponentAwaitProductThumb from './ComponentAwaitProductThumb';
import ComponentProductThumbNull from './ComponentProductThumbNull';

import { Card, Inset, Info, Op, Title, Price, Unavailable, Draft, OFF } from '../styles/Products';

function ComponentMapProducts({ data }) {

  const [id, assoc] = data;
  const { status = 200, title, price, thumb = [], price_off = 0 } = assoc;
  const history = useHistory();
  const { setBar } = SnapshotBar();
  const adOptions = () => setBar(id);

  return (
    <Card>
      <Inset>
        {thumb.length
        ? <ComponentAwaitProductThumb {...{ thumb }} />
        : <ComponentProductThumbNull />}
        <Info>
          <Op>
            <button type="button" onClick={() => adOptions()}>
              <svg viewBox="0 0 515.555 515.555" xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="#777777"><path d="m303.347 18.875c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /><path d="m303.347 212.209c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /><path d="m303.347 405.541c25.167 25.167 25.167 65.971 0 91.138s-65.971 25.167-91.138 0-25.167-65.971 0-91.138c25.166-25.167 65.97-25.167 91.138 0" /></svg>
            </button>
          </Op>
          <Title>
            <span>{title}</span>
          </Title>
          <div>
            <Price>{Number(price - price * price_off / 100).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Price>
            {status === 403 && <Unavailable>Indisponível</Unavailable>}
            {status === 203 && <Draft>Em rascunho</Draft>}
            {!!price_off
              && <OFF>{price_off}% OFF</OFF>}
          </div>
        </Info>
      </Inset>
      <button type="button" onClick={() => history.push(`/products/${data[0]}`)}></button>
    </Card>
  )
};

export default ComponentMapProducts;
