import React, { useState, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal } from '../contexts/Service';

import { Main, Title, MSG, Footer, Simple, Button } from '../styles/Decline';

function ComponentDeclineMain({ id, close }) {

  const clickRef = useRef(true);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const [useAttempts, setAttempts] = useState(0);

  const removeFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('removeOrder')({ token, id })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (!status) {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };

        close();
        clickRef.current = true;
        setModal({ status: false });
      })
      .catch(function() {
        clickRef.current = true;
        setModal({ status: false });
        toast.error('Erro na chamada API', Bracael.toast_options);
      });
    };
  }

  return (
    <Main>
      <Title>Ei, com calma!</Title>
      <MSG>Você quer mesmo recusar o pedido de Natanael Lourenço, pois, ao recusar o pedido ele será removido e não será possivél devolver para cá. Deseja recusar?</MSG>
      <Footer>
        <Simple type="button" onClick={() => close()}>Cancelar</Simple>
        <Button type="button" onClick={() => removeFn()}>Recusar pedido!</Button>
      </Footer>
    </Main>
  );
}

export default ComponentDeclineMain;
