import React from 'react';

import ComponentAdditionalName from './ComponentAdditionalName';
import ComponentAdditionalLimit from './ComponentAdditionalLimit';
import ComponentAdditionalPrice from './ComponentAdditionalPrice';
import ComponentAdditionalRemove from './ComponentAdditionalRemove';
import ComponentAdditionalDescription from './ComponentAdditionalDescription';

import { Li, Row, Column } from '../styles/Additional';

function ComponentMapAdditional({ data, i, useExtra, setExtra }) {

  return (
    <Li>
      <Row>
        <ComponentAdditionalName {...{ data, useExtra, setExtra }} />
        <ComponentAdditionalLimit {...{ data, useExtra, setExtra }} />
        {i !== 0
        ? <ComponentAdditionalRemove {...{ data, useExtra, setExtra }} />
        : <Column width="25%" />}
      </Row>
      <Row>
        <ComponentAdditionalDescription {...{ data, useExtra, setExtra }} />
        <ComponentAdditionalPrice {...{ data, useExtra, setExtra }} />
      </Row>
    </Li>
  )
}

export default ComponentMapAdditional;
