import React, { useRef, useState, useEffect } from 'react';
import { HashRouter } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import styled from 'styled-components';

import { database } from './services/server';
import ElementProvider from './contexts/App';

import ComponentPushmenu from './components/ComponentPushmenu';
import ComponentMain from './components/ComponentMain';
import ComponentSidebar from './components/ComponentSidebar';
import ComponentOverlay from './components/ComponentOverlay';
import ComponentSwipe from './components/ComponentSwipe';

// import 'react-toastify/dist/ReactToastify.css';

function App(snapshot) {

  const { token, ...rest } = snapshot;

  const statusRef = useRef(false);

  const { status = false } = rest?.profile || {};
  const businessRef = useRef(rest);
  const tokenRef = useRef(token);
  const [useStatus, setStatus] = useState(status);
  const [useOverlay, setOverlay] = useState(false);
  const [useSwipe, setSwipe] = useState(false);
  const [useWhere, setWhere] = useState(document.querySelector('[name=local]').content);

  useEffect(() => {
    database()
    .ref(`${useWhere}/business/outside/profile/status`)
    .on('value', snapshot => {
      if (statusRef.current) {
        setStatus(snapshot.val() || false);
      }
      else {
        statusRef.current = true;
      };
    });
  }, [useWhere]);

  return (
    <HashRouter>
      <ElementProvider {...{ useStatus, setStatus, useOverlay, setOverlay, useSwipe, setSwipe, useWhere, setWhere, tokenRef, businessRef }}>
        <ComponentPushmenu />
        <ComponentMain />
        <ComponentSidebar />
        <ComponentOverlay />
        <ComponentSwipe />
        <StyledContainer />
      </ElementProvider>
    </HashRouter>
  );
}

export default App;

const StyledContainer = styled(ToastContainer).attrs({
  // custom props
})`
  .Toastify__toast {
    font-family: var(--font-poppins);

    @media (max-width: 1039px) {
      min-height: inherit;
      max-width: 320px;
      margin-top: 1.6rem;
      margin-right: 1.6rem;
      margin-left: auto;
      border-radius: 3px;
    }
  }
  .Toastify__toast--error {
    background: #f56464;
    box-shadow: 0 2px 5px 0 rgba(156,3,3, 0.26);
  }
  .Toastify__toast--warning {
    background: #f5f364;
    box-shadow: 0 2px 5px 0 rgba(156, 154, 3, 0.26);
  }
  .Toastify__toast--success {
    background: #64f588;
    box-shadow: 0 2px 5px 0 rgba(3, 156, 36, 0.26);
  }
  .Toastify__toast--info {
    background: #91ccf3;
    box-shadow: 0 2px 5px 0 rgba(3, 63, 156, 0.26);
  }
  .Toastify__toast-body {
    color: #000;
    font-weight: bold;
  }
  .Toastify__progress-bar {
    height: 3px;
  }
  .Toastify__progress-bar--default {
    background: #000;
  }
  .Toastify__close-button--default {
    margin: auto;
  }
  .Toastify__progress-bar {
    background-color: rgba(0,0,0,0.7);
  }
  .Toastify__close-button {
    color: #000;
    margin: auto;
  }
  .Toastify__close-button svg {
    float: left;
  }
`;
