import React, { useEffect, useRef, useState } from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Group, Input, Label } from '../styles/Post';

const { jQuery: $ } = window;

function ComponentEditPrice() {

  const inputRef = useRef(null);
  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();
  const { price = 0 } = usePost;
  const [useInput, setInput] = useState(price ? price.toFixed(2) : '');

  const altInput = (val) => {
    setInput(val);
    setPost((prevState) => Object({
      ...prevState,
      price: isNaN(Number(val))
      ? Number(val.replace(/\./g, '').replace(/,/g, '.'))
      : Number(val)
    }));
  };

  useEffect(() => {
    const { current } = inputRef;
    current.value = useInput;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useInput]);

  useEffect(() => {
    const { current } = inputRef;
    $(current).mask('#.##0,00', { reverse: true });
  }, [inputRef]);

  return (
    <Group>
      <Input type="text" ref={inputRef} placeholder="0,00" status={true}
        onInput={(e) => altInput(e.target.value)}
        autoCapitalize="off" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off" />
      <Label>R$</Label>
    </Group>
  )
}

export default ComponentEditPrice;
