import React, { useState } from 'react';

import Bracael from '../Bracael';
import { SnapshotList, SnapshotData } from '../contexts/ListProducts';

import { Main, Title, Footer, Simple, Button } from '../styles/ModalForm';
import { Column, Li, Input, Label } from '../styles/Filter';

function ComponentFilterMain({ close }) {

  const { data } = SnapshotData();
  const { setList } = SnapshotList();
  const [useInput, setInput] = useState(-1);

  const removeFn = () => {
    close();
    if (useInput !== -1) {
      setList(Object.fromEntries(Object.entries(data)
      .filter((data) => (data[1].status || 200) === useInput)));
    }
    else {
      setList(data);
    };
  };

  return (
    <Main>
      <Title>Filtrar resultados</Title>
      <Column>
        {[{ status: -1, name: 'Exibir tudo' }, ...Bracael.post_status].map((data) => {
          const { status, name } = data;
          return (
            <Li key={status}>
              <Input type="checkbox" readOnly checked={status === useInput} />
              <Label onClick={() => setInput(status)}>{name}</Label>
            </Li>
          )
        })}
      </Column>
      <Footer>
        <Simple type="button" onClick={() => close()}>Cancelar</Simple>
        <Button type="button" onClick={() => removeFn()}>Filtrar</Button>
      </Footer>
    </Main>
  )
}

export default ComponentFilterMain;
