import React from 'react';
import { useAsync } from "react-async";

import { pathImageUrl } from '../app/'

import { storage } from '../services/server';
import { SnapshotWhere } from '../contexts/App';

import ComponentGalleryListall from './ComponentGalleryListall';
import ComponentLoading from './ComponentLoading';

const fetchPerson = async ({ useWhere }, { signal }) => {

  return await storage()
  .ref(pathImageUrl({ where: useWhere }))
  .listAll()
  .then((data) => data.items)
  .then((data) => data.filter((val) => val.name.indexOf('_160x160.jpeg') !== -1))
  .then((data) => Promise.all(data.map(async (val) => await val.getMetadata())))
  .then((data) => Promise.all(data.map(async (snapshot) => {
    const { fullPath } = snapshot;
    return {
      assoc: snapshot,
      url: await storage()
        .ref(fullPath)
        .getDownloadURL()
    }
  })));
};

function ComponentAwaitImages() {

  const { useWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere });

  if (error) return error.message
  if (data) return <ComponentGalleryListall {...{ data }} />
  return <ComponentLoading />
}

export default ComponentAwaitImages;
