import React from 'react';

import { Content, Name, Mainly, Over, Subname, HREF } from '../styles/Modify';

function ComponentMapFrete({ data, useDelete, setDelete }) {

  const [id, assoc] = data;
  const { district, zone, rate } = assoc;
  const hrefFn = () => setDelete({ status: true, id });

  return (
    <Content>
      <Name>{district}{zone ? ` (${zone})` : ''}</Name>
      <Mainly>
        <Over>
          <Subname>{Number(rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Subname>
          <HREF type="button" onClick={() => hrefFn()}>Excluir</HREF>
        </Over>
      </Mainly>
    </Content>
  );
}

export default ComponentMapFrete;
