import React from 'react';
import { useHistory } from 'react-router-dom';

import { Link } from '../styles/Settings';
import { Content } from '../styles/Cupom';

function ComponentCupomLinks() {

  const history = useHistory();

  return (
    <Content>
      <div>
        <Link type="button" onClick={() => history.push('/settings?section=cupons')}>Gerenciar meus cupons</Link>
      </div>
      <div>
        <Link type="button" onClick={() => history.push('/settings?section=cupom')}>Criar novo cupom</Link>
      </div>
    </Content>
  )
}

export default ComponentCupomLinks;
