import React from 'react';

import { SnapshotOrder, SnapshotBar, SnapshotDelete } from '../contexts/Service';

import { Modal, Close, Content, About, Buttons } from '../styles/Bar';

function ComponentServiceBar() {

  const { useBar, setBar } = SnapshotBar();
  const { useOrder } = SnapshotOrder();
  const { setDelete } = SnapshotDelete();
  const { display_name } = useOrder[useBar];

  const cancenlFn = () => setBar('');
  const removeFn = () => setDelete({ id: useBar, status: true });

  return (
    <Modal backgroundColor="rgba(0,0,0,0)">
      <Close onClick={() => cancenlFn()} />
      <Content>
        <About>
          <button type="button" onClick={() => cancenlFn()}>
            <svg height="16" viewBox="0 0 386.667 386.667" width="16" xmlns="http://www.w3.org/2000/svg" fill="#ffffff"><path d="m386.667 45.564-45.564-45.564-147.77 147.769-147.769-147.769-45.564 45.564 147.769 147.769-147.769 147.77 45.564 45.564 147.769-147.769 147.769 147.769 45.564-45.564-147.768-147.77z" /></svg>
          </button>
          <div>
            <span>{display_name}</span>
          </div>
        </About>
        <Buttons>
          <button type="button" onClick={() => removeFn()}>
            <svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 515.556 515.556" data-name="trash" fill="#ffffff"><path d="m64.444 451.111c0 35.526 28.902 64.444 64.444 64.444h257.778c35.542 0 64.444-28.918 64.444-64.444v-322.222h-386.666z"></path><path d="m322.222 32.222v-32.222h-128.889v32.222h-161.111v64.444h451.111v-64.444z"></path></svg>
          </button>
        </Buttons>
      </Content>
    </Modal>
  );
}

export default ComponentServiceBar;
