import React from 'react';

import { Main, Title, MSG, Footer, Simple, Button } from '../styles/ModalForm';

function ComponentFormModalSimple({ valuesForm }) {

  const { title, message, buttons, confirmFn, close } = valuesForm;
  const [cancel, confirm] = buttons;

  return (
    <Main>
      <Title>{title}</Title>
      <MSG>{message}</MSG>
      <Footer>
        <Simple type="button" status={(cancel.status || false)} onClick={() => close()}>{cancel.value}</Simple>
        <Button type="button" status={(confirm.status || false)} onClick={() => confirmFn()}>{confirm.value}</Button>
      </Footer>
    </Main>
  );
}

export default ComponentFormModalSimple;
