import React, { useState, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Name, Main, Over, Under, Value, Textarea, Subname, HREF, Footer, Simple, Button } from '../styles/Modify';

function ComponentContainerAbout({ useShow, setShow }) {

  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { about = {} } = useOutside.container || {};
  const [useInput, setInput] = useState(about);
  const { content = '' } = useInput;

  const isValid = () => ((about.content || '') !== content);
  const inputFn = (val) => setInput((prevState) => Object({
    ...prevState,
    content: val.replace(/\n/gi, "\n")
  }));
  const cancelFn = () => {
    setInput(about);
    setShow(-1);
  };
  const saveFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions().httpsCallable('setBusiness')({
        token,
        ref: 'container/about',
        data: { content, update: new Date().toISOString() }
      })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!', Bracael.toast_options);
          setShow(-1);
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  return (
    <Content>
      <Name>Sobre a empresa</Name>
      <Main status={useShow === 1}>
        <Over>
          <Subname>{(content || 'Lorem Ipsum')}</Subname>
          <HREF type="button" onClick={() => setShow(1)}>Editar</HREF>
        </Over>
        <Under>
          <Value>
            <Textarea type="text" placeholder="Lorem Ipsum" onChange={(e) => inputFn(e.target.value)} value={content} />
          </Value>
          <Footer>
            <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Salvar alterações</Button>
            <Simple type="button" onClick={() => cancelFn()}>Cancelar</Simple>
          </Footer>
        </Under>
      </Main>
    </Content>
  );
}

export default ComponentContainerAbout;
