import React from 'react';

import { SnapshotSection } from '../contexts/ProductId';

import ComponentBreadcrumbHome from './ComponentBreadcrumbHome';
import ComponentBreadcrumbSelector from './ComponentBreadcrumbSelector';
import ComponentBreadcrumbAdditional from './ComponentBreadcrumbAdditional';

function ComponentPluginsBreadcrumb() {

  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection();

  if (useSection === 2) return <ComponentBreadcrumbAdditional />
  if (useSection === 3) return <ComponentBreadcrumbSelector />
  return <ComponentBreadcrumbHome />
}

export default ComponentPluginsBreadcrumb;
