import React from 'react';

import { Column, Input } from '../styles/Additional';

function ComponentAdditionalDescription({ data, useExtra, setExtra }) {

  // eslint-disable-next-line no-unused-vars
  const [id, assoc] = data;
  const { about = '' } = assoc;

  const inputFn = (val) => setExtra((prevState) => {
    const { ...rest } = prevState[id] || {};
    return {
      ...prevState,
      [id] : {
        ...rest,
        about: val
      }
    };
  });

  return (
    <Column>
      <Input type="text" placeholder="Descrição"
        onInput={(e) => inputFn(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={about} />
    </Column>
  );
}

export default ComponentAdditionalDescription;
