import React from 'react';

import {
  SnapshotData,
  SnapshotBar,
  SnapshotDraftOn,
  SnapshotDraftOff,
  SnapshotUnvOn,
  SnapshotUnvOff,
  SnapshotDelete
} from '../contexts/ListProducts'

import { Modal, Close, Content, About, Buttons, Title, Text } from '../styles/Bar';

function ComponentModalBar() {

  const { setDraftOn } = SnapshotDraftOn();
  const { setDraftOff } = SnapshotDraftOff();
  const { setUnvOn } = SnapshotUnvOn();
  const { setUnvOff } = SnapshotUnvOff();
  const { setDelete } = SnapshotDelete();
  const { useBar, setBar } = SnapshotBar();
  const { data } = SnapshotData();
  const { title = '', status = 200 } = data[useBar];

  const cancenlFn = () => setBar('');

  const draftFn = () => {
    if (status === 203) {
      setDraftOff({ id: useBar, status: true });
    }
    else {
      setDraftOn({ id: useBar, status: true });
    };
  };

  const unavailableFn = () => {
    if (status === 403) {
      setUnvOff({ id: useBar, status: true });
    }
    else {
      setUnvOn({ id: useBar, status: true });
    };
  };

  const removeFn = () => {
    setDelete({ id: useBar, status: true });
  };

  return (
    <Modal backgroundColor="rgba(0,0,0,0)">
      <Close onClick={() => cancenlFn()} />
      <Content>
        <About>
          <button type="button" onClick={() => cancenlFn()}>
            <svg height="16" viewBox="0 0 386.667 386.667" width="16" xmlns="http://www.w3.org/2000/svg" fill="#ffffff"><path d="m386.667 45.564-45.564-45.564-147.77 147.769-147.769-147.769-45.564 45.564 147.769 147.769-147.769 147.77 45.564 45.564 147.769-147.769 147.769 147.769 45.564-45.564-147.768-147.77z" /></svg>
          </button>
          <Title>
            <Text>{title}</Text>
          </Title>
        </About>
        <Buttons>
          <button type="button" tabIndex="0" onClick={() => draftFn()}>
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 18 12" fill="#ffffff"><path d="M9,0C4,0,0,4,0,6s4,6,9,6,9-4,9-6S14,0,9,0ZM9,10a4,4,0,1,1,4-4A4,4,0,0,1,9,10Z"/><path d="M10.5,5A1.5,1.5,0,0,1,9.09,4H9a2,2,0,1,0,2,2,2,2,0,0,0-.29-1Z"/></svg>
          </button>
          <button type="button" tabIndex="0" onClick={() => unavailableFn()}>
            <svg height="16" viewBox="0 0 515.556 515.556" width="16" xmlns="http://www.w3.org/2000/svg" fill="#ffffff"><path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm128.889 290h-257.778v-64.444h257.778z" /></svg>
          </button>
          <button type="button" tabIndex="0" onClick={() => removeFn()}>
            <svg height="16" xmlns="http://www.w3.org/2000/svg" width="16" viewBox="0 0 384 384" fill="#ffffff"><path d="M64,341.333C64,364.907,83.093,384,106.667,384h170.667C300.907,384,320,364.907,320,341.333v-256H64V341.333z" /><polygon points="266.667,21.333 245.333,0 138.667,0 117.333,21.333 42.667,21.333 42.667,64 341.333,64 341.333,21.333" /></svg>
          </button>
        </Buttons>
      </Content>
    </Modal>
  );
}

export default ComponentModalBar;
