import React, { useState, useRef, useEffect } from 'react';

import Bracael from '../Bracael';
import { SnapshotPost } from '../contexts/ProductId';

import ComponentModalWeek from './ComponentModalWeek';

import { Content, Main, Name, Open } from '../styles/Week';

const { jQuery: $ } = window;

function ComponentEditWeek() {

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [useModal, setModal] = useState(false);
  const { usePost } = SnapshotPost();
  const { available = {} } = usePost;

  const organize = (a, b) => Bracael.u.d.indexOf(a[0]) - Bracael.u.d.indexOf(b[0]);
  const dailyFilter = () => Object.entries(available).filter((data) => data[1]).sort(organize);
  const dailyMap = () => dailyFilter().map((data) => Bracael.u.b[Bracael.u.d.indexOf(data[0])]);

  const daily_text = dailyFilter().length
  ? dailyFilter().length < 5
    ? dailyMap().join(', ').replace(/, ([^,]*)$/, ' e $1')
    : `${dailyMap().filter((val, i) => i < 5).join(', ')}...`
  : 'Selecione um dia!';

  useEffect(() => {
    if (statusRef.current) {
      if (!useModal) {
        $(modalRef.current).fadeOut(300);
      };
    }
    else {
      statusRef.current = true;
    };
  }, [useModal]);

  return (
    <Content>
      <Main>
        <Name>Dias para a promoção</Name>
        <small>{daily_text}</small>
      </Main>
      <Open type="button" onClick={() => setModal((prevState) => !prevState)}>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 10 21" width="22" height="21"><path fill="currentColor" d="M1 15.75l5.2-5.2L1 5.35l1.5-1.5 6.5 6.7-6.6 6.6-1.4-1.4z"></path></svg>
      </Open>
      <ComponentModalWeek {...{ useModal, setModal, statusRef, modalRef }} />
    </Content>
  );
}

export default ComponentEditWeek;
