import styled from "styled-components";

export const Div = styled.div`
  height: ${({ height }) => (typeof height === "number") ? `${height}px` : height};
  margin-bottom: ${({ marginBottom }) => (typeof marginBottom === "number") ? `${marginBottom}px` : marginBottom};
  margin-left: ${({ marginLeft }) => (typeof marginLeft === "number") ? `${marginLeft}px` : marginLeft};
  margin-right: ${({ marginRight }) => (typeof marginRight === "number") ? `${marginRight}px` : marginRight};

  width: ${({ width }) => (typeof width === "number") ? `${width}px` : width};
  max-width: ${({ maxWidth }) => (typeof maxWidth === "number") ? `${maxWidth}px` : maxWidth};
  min-width: ${({ minWidth }) => (typeof minWidth === "number") ? `${minWidth}px` : minWidth};

  display: ${({ displayFlex }) => displayFlex && 'flex'};

  justify-content: ${({ justifyContent }) => justifyContent && justifyContent};
  align-items: ${({ alignItems }) => alignItems && alignItems};

  & > *:not(:last-child) {
    margin-top: ${({ spaceTopChild }) => spaceTopChild && spaceTopChild};
    margin-right: ${({ spaceRightChild }) => spaceRightChild && spaceRightChild};
    margin-left: ${({ spaceLeftChild }) => spaceLeftChild && spaceLeftChild};
    margin-bottom: ${({ spaceBottomChild }) => spaceBottomChild && spaceBottomChild};
  }

  & > * {
    display: flex;
  }

  & * {
    float: ${({ floatChild }) => floatChild && floatChild};
  }
`

export const Child = styled.div`
  display: flex;
`
