import styled from 'styled-components';

import { BorderRadius } from './root';

export const Content = styled.div`
  flex-direction: column;
  display: flex;
  height: 100%;
`

export const Head = styled.div`
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.25) 0 0 16px;
  z-index: 1;

  & ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    margin: 0 auto;

    @media (min-width: 1040px) {
      max-width: 600px;
    }
  }

  & > * {
    width: 100%;
  }
`

export const Radio = styled.li`
  position: relative;

  & input:checked ~ label::after {
    background-color: ${({ status }) => status ? '#b5b5b5' : '#cc0000'};
    width: 100%;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    left: 0;
    right: 0;
  }

  & input:not(:checked) ~ label:hover::after {
    background-color: ${({ status }) => status ? '#b5b5b5' : 'rgba(170, 170, 170, 0.45)'};
    width: 60%;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    left: 20%;
    right: 20%;
  }

  & label {
    color: ${({ status }) => status ? '#b5b5b5' : '#999'};
    max-width: 150px;
    padding: 1.8rem 1.6rem;
    float: left;
    width: 100%;
    cursor: pointer;
    text-align: center;
    transition: color .3s;
    font-weight: 500;
    font-family: var(--font-poppins);
    font-size: 1.6rem;

    @media (min-width: 1040px) {
      max-width: initial;
    }

    &:hover {
      color: ${({ status }) => status ? '#b5b5b5' : '#404040'};
    }

    &::after {
      content: "";
      display: block;
      width: 20%;
      height: 3px;
      left: 40%;
      right: 40%;
      transition: 0.3s;
      background-color: transparent;
      position: absolute;
      bottom: 0;
    }
  }
`

export const Main = styled.div`
  width: 100%;
  overflow: hidden;
  display: block;
  height: 100%;
`

export const Li = styled.div`
  width: calc(25% - 0.75rem);
  opacity: ${({ status }) => status ? '0.55' : '1'};

  @media (min-width: 1040px) {
    width: calc(20% - 1.2792rem);
  }
`

export const IMG = styled.div`
  background-size: cover;
  background-position: center center;
  background-color: rgb(232, 232, 232);
  position: relative;
  cursor: pointer;
  height: 100%;

  ${BorderRadius(3)}

  & img {
    float: left;
  }
`
