import React from 'react';

import { SnapshotSwitch } from '../contexts/Gallery';

import ComponentGallerySelect from './ComponentGallerySelect';
import ComponentGalleryDelete from './ComponentGalleryDelete';

import { Content, Desktop, Main, Head, Status, Name, Small } from '../styles/Images';

function ComponentGalleryListall({ data }) {

  const { useSwitch, setSwitch } = SnapshotSwitch();

  return data.length ? (
    <Content>
      <Desktop>
        <Head>
          <Status>
            <input type="checkbox" readOnly checked={useSwitch} />
            <label onClick={() => setSwitch((prevState) => !prevState)}>{useSwitch ? 'Selecionar' : 'Deletar'}</label>
          </Status>
          <Name>{data.length} imagens no total</Name>
        </Head>
        <Main>
          {useSwitch
          ? data.map((data) => <ComponentGallerySelect key={data.assoc.generation} {...{ data }} />)
          : data.map((data) => <ComponentGalleryDelete  key={data.assoc.generation} {...{ data }} />)}
        </Main>
      </Desktop>
    </Content>
  ) : <Small>Por enquanto, não há nada por aqui.</Small>;
}

export default ComponentGalleryListall;
