import React, { useEffect, useRef, useState } from 'react';

import { database } from '../services/server';
import { SnapshotWhere } from '../contexts/App';
import ElementProvider from '../contexts/Service';

import ComponentRadioMenu from './ComponentRadioMenu';

import ComponentServiceNew from './ComponentServiceNew';
import ComponentServiceAttendance from './ComponentServiceAttendance';
import ComponentModalStatus from './ComponentModalStatus';
import ComponentModalClient from './ComponentModalClient';
import ComponentModalComanda from './ComponentModalComanda';
import ComponentServiceBar from './ComponentServiceBar';
import ComponentModalDelete from './ComponentModalDelete';
import ComponentModalAccept from './ComponentModalAccept';
import ComponentModalDecline from './ComponentModalDecline';

import { Container, Content, Main, Column, Desktop, Small } from '../styles/Service';

var myAudio = new Audio('https://sites.google.com/site/holasoycael/u/to-the-point.mp3');

function ComponentService({ data, useModal, setModal }) {

  const { feed, cupons, order, delivery } = data;
  const dataRef = useRef({ feed, cupons, order, delivery });
  const databaseRef = useRef(null);
  const realtimeRef = useRef(false);
  const statusRef = useRef(false);
  const contentRef = useRef(null);
  const serviceRef = useRef(null);
  const [useClient, setClient] = useState({});
  const [useComanda, setComanda] = useState({});
  const [useStatus, setStatus] = useState({});
  const [useDelete, setDelete] = useState({});
  const [useAccept, setAccept] = useState({});
  const [useDecline, setDecline] = useState({});
  const [useBar, setBar] = useState('');
  const [useScreen, setScreen] = useState(1);

  const organize = (a, b) => (a[1].timestamp < b[1].timestamp) ? 1 : ((b[1].timestamp < a[1].timestamp) ? -1 : 0);

  const [useOrder, setOrder] = useState(order);
  const { useWhere } = SnapshotWhere();

  useEffect(() => {
    if (statusRef.current) {
      if (useScreen === 2) {
        serviceRef.current.style.left = "-100%";
      }
      else {
        serviceRef.current.style.left = null;
      };
    }
    else {
      statusRef.current = true;
    };
  }, [useScreen]);

  useEffect(() => {
    databaseRef.current = database()
    .ref(`${useWhere}/order`)
    .on('value', snapshot => {
      if (contentRef.current) {
        if (realtimeRef.current) {
          setOrder((snapshot.val() || {}));
          if (Object.keys(useOrder).length < Object.keys(snapshot.val() || {}).length) {
            myAudio.play();
            myAudio.muted = false;
          };
        }
        else {
          realtimeRef.current = true;
        };
      }
      else {
        databaseRef.current = null;
      };
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [databaseRef]);

  return (
    <Container ref={contentRef}>
      <ElementProvider {...{ useModal, setModal, useOrder, setOrder, useClient, setClient, useComanda, setComanda, useStatus, setStatus, useBar, setBar, useDelete, setDelete, useAccept, setAccept, useDecline, setDecline, dataRef }}>
        <ComponentRadioMenu {...{ useScreen, setScreen }} />
        <Content>
          <Main ref={serviceRef}>
            <Column>
              {Object.entries(useOrder)
              .filter((data) => !data[1].attendance)
              .length
              ? (<Desktop>{Object.entries(useOrder)
                .filter((data) => !data[1].attendance)
                .sort(organize)
                .map((data) => <ComponentServiceNew key={data[0]} {...{ data, cupons, feed, delivery }} />)}
                </Desktop>)
              : <Small>Por enquanto, não há novos pedidos.</Small>}
            </Column>
            <Column>
              {Object.entries(useOrder)
              .filter((data) => !!data[1].attendance)
              .length
              ? (<Desktop> {Object.entries(useOrder)
                .filter((data) => !!data[1].attendance)
                .sort(organize)
                .map((data) => <ComponentServiceAttendance key={data[0]} {...{ data, cupons, feed, delivery }} />)}
                </Desktop>)
              : <Small>Por enquanto, não há pedidos em atendimento.</Small>}
            </Column>
          </Main>
          <ComponentModalStatus />
          <ComponentModalClient />
          <ComponentModalComanda />
          <ComponentModalDelete />
          <ComponentModalAccept />
          <ComponentModalDecline />
          {useOrder[useBar] ? <ComponentServiceBar /> : null}
        </Content>
      </ElementProvider>
    </Container>
  );
}

export default ComponentService;
