import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useModal, setModal, useOrder, setOrder, useClient, setClient, useComanda, setComanda, useStatus, setStatus, useBar, setBar, useDelete, setDelete, useAccept, setAccept, useDecline, setDecline, dataRef }) {

  return (
    <ElementContext.Provider value={{ useModal, setModal, useOrder, setOrder, useClient, setClient, useComanda, setComanda, useStatus, setStatus, useBar, setBar, useDelete, setDelete, useAccept, setAccept, useDecline, setDecline, dataRef }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotOrder() {
  const context = useContext(ElementContext)
  const { useOrder, setOrder } = context;
  return { useOrder, setOrder };
}

export function SnapshotClient() {
  const context = useContext(ElementContext)
  const { useClient, setClient } = context;
  return { useClient, setClient };
}

export function SnapshotComanda() {
  const context = useContext(ElementContext)
  const { useComanda, setComanda } = context;
  return { useComanda, setComanda };
}

export function SnapshotStatus() {
  const context = useContext(ElementContext)
  const { useStatus, setStatus } = context;
  return { useStatus, setStatus };
}

export function SnapshotBar() {
  const context = useContext(ElementContext)
  const { useBar, setBar } = context;
  return { useBar, setBar };
}

export function SnapshotModal() {
  const context = useContext(ElementContext)
  const { useModal, setModal } = context;
  return { useModal, setModal };
}

export function SnapshotDelete() {
  const context = useContext(ElementContext)
  const { useDelete, setDelete } = context;
  return { useDelete, setDelete };
}

export function SnapshotDecline() {
  const context = useContext(ElementContext)
  const { useDecline, setDecline } = context;
  return { useDecline, setDecline };
}

export function SnapshotAccept() {
  const context = useContext(ElementContext)
  const { useAccept, setAccept } = context;
  return { useAccept, setAccept };
}

export function SnapshotData() {
  const context = useContext(ElementContext)
  const { dataRef } = context;
  return { ...dataRef.current };
}
