import React from 'react';

import { Content, Row, Column, Input, Label } from '../styles/Radio';

function ComponentRadioMenu({ useScreen, setScreen }) {

  return (
    <Content>
      <Row>
        <Column>
          <Input type="radio" readOnly checked={useScreen === 1} />
          <Label onClick={() => (useScreen !== 1) && setScreen(1)}>Novos pedidos</Label>
        </Column>
        <Column>
          <Input type="radio" readOnly checked={useScreen === 2} />
          <Label onClick={() => (useScreen !== 2) && setScreen(2)}>Em atendimento</Label>
        </Column>
      </Row>
    </Content>
  );
};

export default ComponentRadioMenu;
