import React, { useEffect } from 'react';
import { useAsync } from 'react-async';

import { pathImageUrl } from '../app/';

import { storage } from '../services/server';
import { SnapshotWhere } from '../contexts/App';

import ComponentUploadSucess from './ComponentUploadSucess';
import ComponentUploadLoad from './ComponentUploadLoad';

const fetchPerson = async ({ useIMG, useWhere }, { signal }) => {

  return await Promise.all(useIMG.map(async (data, i) => {

    const { file, type } = data;
    const { name } = file;

    return await storage()
      .ref(`${pathImageUrl({ where: useWhere })}/${(+new Date()) + '-' + name}`)
      .put(file, {
        contentType: type
      })
      .then(snapshot => snapshot.ref.getMetadata());
  }));
}

function ComponentAwaitUpload({ useIMG, setIMG, useSucess, setSucess }) {

  const { useWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useIMG, useWhere });

  useEffect(() => {
    if (data) {
      setSucess(true);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  if (error) return error.message
  if (data) return useIMG.map((data, i) => <ComponentUploadSucess key={i} {...{ data }} />);
  return useIMG.map((data, i) => <ComponentUploadLoad key={i} {...{ data }} />);
}

export default ComponentAwaitUpload;
