import React from 'react';

import Bracael from '../Bracael';
import { SnapshotPost } from '../contexts/ProductId';

import { Content, Main, Button } from '../styles/Category';

function ComponentProductCategory() {

  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();

  const buttonFn = (val) => setPost((prevState) => Object({
    ...prevState,
    category: val
  }));

  return (
    <Content>
      <Main>
        {Bracael.category_names.map((data) => {
          const { keycode, keyname } = data;
          return (
            <Button type="button" key={keycode} onClick={() => buttonFn(keycode)}>
              <span>{keyname}</span>
            </Button>
          );
        })}
      </Main>
    </Content>
  );
}

export default ComponentProductCategory;
