import React, { useEffect, useRef } from 'react';

import { SnapshotOrder, SnapshotClient } from '../contexts/Service';

import ComponentClientMain from './ComponentClientMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalClient() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useClient, setClient } = SnapshotClient();
  const { useOrder } = SnapshotOrder();
  const { id } = useClient;

  useEffect(() => {
    const { status } = useClient;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useClient]);

  useEffect(() => {
    const { status } = useClient;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setClient({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useClient]);

  const cancenlFn = () => setClient((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} backgroundColor="rgba(0,0,0,0.5)" zIndex="6">
      {useOrder[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4" alignItems="center">
          <ComponentClientMain close={cancenlFn} {...{ id }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalClient;
