import React from 'react';

import { Column, Text, Child, Delete } from '../styles/Selector';

function ComponentMapSelectorList({ data, i, useList, setList }) {

  const { subname = '', moment } = data;

  const inputFn = (val) => {
    const assoc = [...JSON.parse(useList)];
    assoc[i].moment = moment;
    assoc[i].subname = val;
    setList(JSON.stringify(assoc));
  };

  const deleteFn = () => {
    const assoc = [...JSON.parse(useList)];
    assoc.splice(i, 1);
    setList(JSON.stringify(assoc));
  };

  return (
    <Column>
      <Text type="text" placeholder={`Item ${i+1}`}
      onChange={(e) => inputFn(e.target.value)} value={subname} />
      {i
      ? (<Child>
          <Delete type="button" onClick={() => deleteFn()}>
            <svg height="16" viewBox="0 0 551.13 551.13" width="16" xmlns="http://www.w3.org/2000/svg"><path d="m516.685 68.891h-137.783v-51.668c0-9.52-7.703-17.223-17.223-17.223h-172.228c-9.52 0-17.223 7.703-17.223 17.223v51.668h-137.782v34.446h34.446v413.348c0 19.006 15.457 34.446 34.446 34.446h344.456c18.989 0 34.446-15.44 34.446-34.446v-413.348h34.446v-34.446zm-310.011-34.445h137.783v34.446h-137.783zm241.153 482.239-.034 17.223v-17.223h-344.456v-413.348h344.456v413.348z"/></svg>
          </Delete>
        </Child>)
      : null}
    </Column>
  )
}

export default ComponentMapSelectorList;
