import React from 'react';

import { SnapshotScreen, SnapshotPost, SnapshotSection } from '../contexts/ProductId';

import { Row, GO, DELETE } from '../styles/Extension';

function ComponentExtensionButtons({ useModal, setModal }) {

  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection();
  // eslint-disable-next-line no-unused-vars
  const { useScreen, setScreen } = SnapshotScreen();
  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();
  const { extra = {}, pick = {} } = usePost;

  const altSelectors = () => {
    setScreen(3);
    setSection(3);
  };

  const altRemoveSelectors = () => {
    setModal({
      status: true,
      name: 'pick'
    });
  };

  const altAdditional = () => {
    setScreen(3);
    setSection(2);
  };

  const altRemoveAdditional = () => {
    setModal({
      status: true,
      name: 'extra'
    });
  };

  return (
    <>
      {Object.keys(pick).length
      ? (<Row>
          <GO type="button" onClick={() => altSelectors()}>
            <span>Seletores</span>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 10 21" width="22" height="21"><path fill="currentColor" d="M1 15.75l5.2-5.2L1 5.35l1.5-1.5 6.5 6.7-6.6 6.6-1.4-1.4z"></path></svg>
          </GO>
          <DELETE type="button" onClick={() => altRemoveSelectors()}>
            <svg height="18" viewBox="0 0 515.556 515.556" width="18" xmlns="http://www.w3.org/2000/svg"><path d="m64.444 451.111c0 35.526 28.902 64.444 64.444 64.444h257.778c35.542 0 64.444-28.918 64.444-64.444v-322.222h-386.666z" /><path d="m322.222 32.222v-32.222h-128.889v32.222h-161.111v64.444h451.111v-64.444z" /></svg>
          </DELETE>
        </Row>)
      : null}
      {Object.keys(extra).length
      ? (<Row>
          <GO type="button" onClick={() => altAdditional()}>
            <span>Ingredientes adicionais</span>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 10 21" width="22" height="21"><path fill="currentColor" d="M1 15.75l5.2-5.2L1 5.35l1.5-1.5 6.5 6.7-6.6 6.6-1.4-1.4z"></path></svg>
          </GO>
          <DELETE type="button" onClick={() => altRemoveAdditional()}>
            <svg height="18" viewBox="0 0 515.556 515.556" width="18" xmlns="http://www.w3.org/2000/svg"><path d="m64.444 451.111c0 35.526 28.902 64.444 64.444 64.444h257.778c35.542 0 64.444-28.918 64.444-64.444v-322.222h-386.666z" /><path d="m322.222 32.222v-32.222h-128.889v32.222h-161.111v64.444h451.111v-64.444z" /></svg>
          </DELETE>
        </Row>)
      : null}
    </>
  )
}

export default ComponentExtensionButtons;
