import React, { useEffect, useState } from 'react';

import Bracael from '../Bracael';
import { SnapshotPost } from '../contexts/ProductId';

import ComponentMapWeek from './ComponentMapWeek';

import { Modal, Close, Content } from '../styles/Modal';
import { Row, Title, Daily, Footer, Simple, Button } from '../styles/Week';

const { jQuery: $ } = window;

function ComponentModalWeek({ useModal, setModal, statusRef, modalRef }) {

  const { usePost, setPost } = SnapshotPost();
  const { available = {} } = usePost;

  const [useDaily, setDaily] = useState(available);

  useEffect(() => {
    if (statusRef.current && useModal) {
      $(modalRef.current).fadeIn(200);
    };
  }, [statusRef, modalRef, useModal]);

  const altClose = () => {
    setDaily(available);
    setModal((prevState) => !prevState);
  };

  const altSave = () => {
    setModal((prevState) => !prevState);
    setPost((prevState) => Object({
      ...prevState,
      available: useDaily
    }));
  };

  const isValid = () => Bracael.u.d.map((id) => !!useDaily[id] === !!available[id]).includes(false);

  return (
    <Modal ref={modalRef}>
      <Close onClick={() => altClose()} />
      <Content>
        <Row>
          <Title>Quais os dias?</Title>
          <Daily>
            {Bracael.u.d.map((data, i) => <ComponentMapWeek key={i} {...{ data, i, useDaily, setDaily }} />)}
          </Daily>
          <Footer>
            <Simple type="button" onClick={() => altClose()}>Fechar</Simple>
            <Button type="button" status={isValid()} onClick={() => isValid() && altSave()}>Salvar</Button>
          </Footer>
        </Row>
      </Content>
    </Modal>
  );
}

export default ComponentModalWeek;
