import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import ComponentEditThumb from './ComponentEditThumb';
import ComponentEditTitle from './ComponentEditTitle';
import ComponentEditPrice from './ComponentEditPrice';
import ComponentEditPriceOff from './ComponentEditPriceOff';
import ComponentEditGrams from './ComponentEditGrams';
import ComponentEditMilliliter from './ComponentEditMilliliter';
import ComponentEditDesciption from './ComponentEditDesciption';
import ComponentEditWeek from './ComponentEditWeek';
import ComponentEditExtension from './ComponentEditExtension';

import { Basic, Main } from '../styles/Post';

function ComponentProductMain() {

  const { usePost } = SnapshotPost();
  const { category } = usePost;

  return (
    <Main>
      <Basic>
        <ComponentEditThumb />
        <ComponentEditTitle />
        <ComponentEditPrice />
        <ComponentEditPriceOff />
        {category !== '003' ? <ComponentEditGrams /> : <ComponentEditMilliliter />}
        {category !== '003' && <ComponentEditDesciption />}
        {category === '005' && <ComponentEditWeek />}
        <ComponentEditExtension />
      </Basic>
    </Main>
  );
}

export default ComponentProductMain;
