import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';

import ComponentProducts from './ComponentProducts';
import ComponentSkeletonProducts from './ComponentSkeletonProducts';

const fetchPerson = async ({ token }, { signal }) => await functions()
  .httpsCallable('queryObject')({ token, ref: 'feed' })
  .then((data) => data.data);

function ComponentAwaitProducts() {

  const { token } = SnapshotToken();
  const { data, error } = useAsync({ promiseFn: fetchPerson, token });

  if (error) return error.message
  if (data) return <ComponentProducts {...{ data }} />
  return <ComponentSkeletonProducts />
};

export default ComponentAwaitProducts;
