import styled from "styled-components";

import { SimpleHoverButton } from "./root";

export const Pushmenu = styled.aside`
  width: 100vw;
  max-width: 256px;
  color: var(--color-clean-solid);
  background: var(--color-aside-bg);
  transition: all .3s ease-in-out;
  height: 100%;
  z-index: 5;

  @media(max-width: 800px) {
    transform: translate3d(-100%, 0, 0);
    max-width: 240px;
    position: fixed;
    left: 0;
  }
`

export const Button = styled.div`
  width: 32px;
  right: calc(-32px + -1.6rem);
  display: none;
  z-index: 3;
  position: absolute;
  border-radius: 50%;
  height: 32px;
  top: 0;

  @media(max-width: 720px) {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 7.5px auto;
  }

  &, & * {
    transition: .25s linear;
  }

  & i, & i::before, & i::after {
    position: absolute;
    width: 18px;
    height: 2px;
    transition: transform .3s linear, top .3s linear, background .25s linear;
    background: #777777;
  }

  & button {
    width: 18px;
    height: 18px;
    display: block;
    box-sizing: initial;
    position: relative;
    border-radius: 50%;
    padding: 7px;
  }

  & i {
    top: 15px;
    left: 7px;

    &::before, &::after {
      content: '';
      left: 0;
    }
    &::before {
      top: 5px;
    }
    &::after {
      top: -5px;
    }
  }

  ${({ useSwipe }) => useSwipe ? `
    margin: initial !IMPORTANT;
    right: -48px;
    width: 48px;
    height: 48px;

    background: var(--color-aside-bg);
    -webkit-border-radius: 0 0 3px 0;
    -moz-border-radius: 0 0 3px 0;
    -ms-border-radius: 0 0 3px 0;
    -o-border-radius: 0 0 3px 0;
    border-radius: 0 0 3px 0;

    & button {
      padding: 15px;

      & i, & i::before, & i::after {
        background: #ffffff;
      }

      & i {
        top: 23px;
        background: none;
        left: 12px;

        &::before, &::after {
          top: 0;
          width: 24px;
        }

        &::before {
          -webkit-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
        }

        &::after {
          -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          transform: rotate(45deg);
        }
      }
    }
  ` : `
    & button{
      ${SimpleHoverButton}
    }
  `}
`

export const Pushbox = styled.div`

  & ul {
    overflow-x: hidden;
    overflow-y: overlay;
    height: calc(100vh - 48px);
    box-shadow: 0 1px 0 var(--color-aside-border) inset;

    & ol {
      font-weight: 500;
      padding: 12px 0;
      box-shadow: 0 -1px 0 var(--color-aside-border) inset;
    }
  }
`

