import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Service from '../routes/Service';
import Products from '../routes/Products';
import ProductsId from '../routes/ProductsId';
import Settings from '../routes/Settings';
import CreateProduct from '../routes/CreateProduct';

function ComponentRouters() {

  return (
    <Switch>
      <Route path="/" exact component={Service} />
      <Route path="/products/:id" component={ProductsId} />
      <Route path="/products" component={Products} />
      <Route path="/settings" component={Settings} />
      <Route path="/post" component={CreateProduct} />
    </Switch>
  );
}

export default ComponentRouters;
