import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useSection, setSection, useDelete, useAdd, setAdd, setDelete, useSwitch, setSwitch, useAwait, setAwait, clickRef }) {

  return (
    <ElementContext.Provider value={{ useSection, setSection, useDelete, useAdd, setAdd, setDelete, useSwitch, setSwitch, useAwait, setAwait, clickRef }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotSection() {
  const context = useContext(ElementContext)
  const { useSection, setSection } = context;
  return { useSection, setSection };
}

export function SnapshotDelete() {
  const context = useContext(ElementContext)
  const { useDelete, setDelete } = context;
  return { useDelete, setDelete };
}

export function SnapshotAdd() {
  const context = useContext(ElementContext)
  const { useAdd, setAdd } = context;
  return { useAdd, setAdd };
}

export function SnapshotAwait() {
  const context = useContext(ElementContext)
  const { useAwait, setAwait } = context;
  return { useAwait, setAwait };
}

export function SnapshotSwitch() {
  const context = useContext(ElementContext)
  const { useSwitch, setSwitch } = context;
  return { useSwitch, setSwitch };
}

export function SnapshotRef() {
  const context = useContext(ElementContext)
  const { clickRef } = context;
  return { clickRef };
}
