import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Main, Title, MSG, Footer, Simple, Button } from '../styles/ModalGallery';

function ComponentModalAddIMGMain({ close, name }) {

  const { usePost, setPost } = SnapshotPost();
  const { thumb = [] } = usePost;

  const selectFn = () => {
    if (name && !thumb.map((data) => data.name).includes(name)) {
      const assoc = {...usePost};
      assoc.thumb = [...thumb];
      assoc.thumb.push({ name, moment: new Date().toISOString() });
      setPost(assoc);
      close();
    };
  };

  return (
    <Main>
      <Title>Adicionar imagem</Title>
      <MSG>Ei! Só confirmando: você quer mesmo adicionar esta imagem ao produto?</MSG>
      <Footer>
        <Simple type="button" status={true} onClick={() => close()}>Cancelar</Simple>
        <Button type="button" status={true} onClick={() => selectFn()}>Adicionar</Button>
      </Footer>
    </Main>
  )
}

export default ComponentModalAddIMGMain;
