import React from 'react';
import pt from 'date-fns/locale/pt-BR';
import { format } from 'date-fns';

import Bracael from '../Bracael';

import { SnapshotData, SnapshotOrder } from '../contexts/Service';

import { Content, Main, Title, Li, Name, Text, Simple, Footer, Button } from '../styles/Client';

function ComponentClientMain({ close, id }) {

  const { useOrder } = SnapshotOrder();
  const { delivery, cupons, feed } = SnapshotData();
  const { display_name, timestamp, street, house_number, complement, delivery_select, telephone_number, payment, comment, trade, detail, order_type, cupom_promotion } = useOrder[id];
  const { district, zone, rate = 0 } = order_type !== 2 ? delivery[delivery_select] : {};
  const { type_off = '001', value = 0 } = cupons[cupom_promotion] || {};
  const { cash, pix, debit = [], credit = [], voucher = [] } = payment;

  const totalCart = type_off === '001'
  ? parseFloat((Bracael.subtotal({ detail, feed }) - Bracael.subtotal({ detail, feed }) * value / 100).toFixed(2))
  : parseFloat((Bracael.subtotal({ detail, feed }) + ~value+1).toFixed(2));

  const formatMethods = () => {
    const user_method = [];
    cash && user_method.push('Dinheiro');
    pix && user_method.push('Pix');
    debit.length && user_method.push('Débito');
    credit.length && user_method.push('Crédito');
    voucher.length && user_method.push('Voucher');

    return `${user_method.join(', ').replace(/, ([^,]*)$/, ' e $1')}`;
  };

  const transformMethod = props => {
    const propsSecond = [...props]
    return props.length === 1 ? props.map(val => val.charAt(0).toUpperCase() + val.slice(1)).join('') : `${props.map((val, i) => i <= 1 ? val.charAt(0).toUpperCase() + val.slice(1) : val).splice(props.lenght - 1, 1)} e ${propsSecond[propsSecond.length - 1]}`
  }

  return (
    <Content>
      <Title>
        <span>{display_name}</span>
      </Title>
      <Main>
        {order_type !== 2
        ? (<Li>
            <Name>Endereço</Name>
            <Text>{street}, {house_number}{complement ? ` - ${complement}` : ''}<br />{district}{zone !== '' ? ` - ${zone}` : ''}</Text>
          </Li>)
        : (<Li>
            <Name>Tipo de pedido</Name>
            <Text>Retirada no local</Text>
          </Li>)}
        <Li>
          <Name>Telefone</Name>
          <Text>{telephone_number}<br />
          <Simple type="button" onClick={() => window.location.href = `tel:${telephone_number.replace(/[^0-9]+/g, '')}`}>Ligar</Simple> ou <Simple type="button" onClick={() => window.open(`https://api.whatsapp.com/send?phone=55${telephone_number.replace(/[^0-9]+/g, '')}`)}>Whatsapp</Simple></Text>
        </Li>
        <Li>
          <Name>Pagamento</Name>
          <Text>{formatMethods()}</Text>
        </Li>
        {!!cupom_promotion
        && <Li>
          <Name>Cupom</Name>
          <Text>{cupom_promotion}</Text>
        </Li>}
        {!!debit.length
        &&  (<Li>
              <Name>Débito</Name>
              <Text>{transformMethod(debit)}</Text>
            </Li>)}
        {!!credit.length
        &&  (<Li>
              <Name>Crédito</Name>
              <Text>{transformMethod(credit)}</Text>
            </Li>)}
        {!!voucher.length
        &&  (<Li>
              <Name>Voucher</Name>
              <Text>{transformMethod(voucher)}</Text>
            </Li>)}
        {!!comment
        && (<Li>
              <Name>Observação</Name>
              <Text>{comment}</Text>
            </Li>)}
        {!!trade
        && (<Li>
              <Name>Troco para</Name>
              <Text>{Number(trade).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Text>
            </Li>)}
        <Li>
          <Name>TOTAL</Name>
          <Text>{Number(totalCart + rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Text>
        </Li>
      </Main>
      <Footer>
        <p>{format(new Date(timestamp), "dd 'de' MMM'. de' yyyy 'ás' p a", { addSuffix: true, locale: pt, timeZone: 'America/Sao_Paulo' })}</p>
        <Button type="button" onClick={() => close()}>Fechar</Button>
      </Footer>
    </Content>
  );
}

export default ComponentClientMain;
