import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';

import ComponentService from '../components/ComponentService';
import ComponentSkeletonService from '../components/ComponentSkeletonService';

const fetchPerson = async ({ token }, { signal }) => {

  return {
    cupons: await functions().httpsCallable('queryObject')({ token, ref: 'business/cupons' })
      .then((data) => data.data),
    order: await functions().httpsCallable('queryObject')({ token, ref: 'order' })
      .then((data) => data.data),
    feed: await functions().httpsCallable('queryObject')({ token, ref: 'feed' })
      .then((data) => data.data),
    delivery: await functions().httpsCallable('queryObject')({ token, ref: 'business/delivery' })
      .then((data) => data.data)
  }
};

function ComponentAwaitService({ useModal, setModal }) {

  const { token } = SnapshotToken();
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  if (error) return error.message
  if (data) return <ComponentService {...{ data, useModal, setModal }} />
  return <ComponentSkeletonService />
}

export default ComponentAwaitService;
