import React from 'react';

import { Li, Hash, Name, QTD, Price } from '../styles/Comanda';

function ComponentComandaExtra({ assoc, extra }) {

  const [id, data] = assoc;
  const { amount } = data;
  const { name, value } = extra[id];

  return (
    <Li>
      <Hash>--</Hash>
      <Name>Extra de {name}</Name>
      <QTD>{amount}un</QTD>
      <Price>{Number(amount * value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Price>
    </Li>
  );
}

export default ComponentComandaExtra;
