import React, { useRef, useState } from 'react';

import ComponentEditMapThumb from './ComponentEditMapThumb';
import ComponentEditMapThumbNull from './ComponentEditMapThumbNull';
import ComponentEditMapThumb404 from './ComponentEditMapThumb404';

import { Swipeable, IMAGES } from '../styles/Thumb';

function ComponentEditSwipeable({ data }) {

  const swipeRef = useRef(null);
  const [useSwipeable, setSwipeable] = useState({
    isDown: false,
  });

  // eslint-disable-next-line no-unused-vars
  const { isDown, startX, scrollLeft, active } = useSwipeable;

  const MouseDown = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      isDown: true,
      active: true,
      scrollLeft: swipeRef.current.scrollLeft,
      startX: event.pageX - swipeRef.current.offsetLeft
    }));

    if (!active) {
      swipeRef.current.classList.add("active");
    };
  };

  const MouseLeave = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      active: false,
      isDown: false
    }));

    if (active) {
      swipeRef.current.classList.remove("active");
    };
  };

  const MouseUp = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      active: false,
      isDown: false
    }));

    if (active) {
      swipeRef.current.classList.remove("active");
    };
  };

  const MouseMove = (event) => {
    const x = event.pageX - swipeRef.current.offsetLeft;
    const walk = (x - startX) * 1.25;

    event.preventDefault();

    if (!isDown) return; // para a function

    if (isDown) {
      swipeRef.current.scrollLeft = scrollLeft - walk;
    };
  };

  const pictures_list = () => {
    const nullArray = [];
    for (var i = data.length + 1; i <= 8; i++) {
      nullArray.push({
        error: true
      });
    };
    return [...data, ...nullArray];
  };

  return (
    <Swipeable draggable="true" ref={swipeRef}
    onMouseDown={MouseDown}
    onMouseLeave={MouseLeave}
    onMouseUp={MouseUp}
    onMouseMove={MouseMove}>
      <IMAGES>
        {pictures_list()
        .map((data, i) => {

          const { error, assoc, moment } = data;
          const { generation } = assoc || {};

          return error
          ? moment
            ? <ComponentEditMapThumb404 key={moment} {...{ data }} />
            : <ComponentEditMapThumbNull key={i} />
          : <ComponentEditMapThumb key={generation} {...{ data }} />
        })}
      </IMAGES>
    </Swipeable>
  )
}

export default ComponentEditSwipeable;
