import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Name, Main, Over, Under, Value, Form, Section, Input, Subname, HREF, Footer, Simple, Button } from '../styles/Modify';

const { jQuery: $ } = window;

function ComponentBasicAddress({ useShow, setShow }) {

  const zipcodeRef = useRef(null);
  const clickRef = useRef(true);
  const [useBlock, setBlock] = useState(false);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { profile } = useOutside;
  const [useInput, setInput] = useState({
    store_street: profile.store_street,
    store_number: profile.store_number,
    address_ref: profile.address_ref,
    store_district: profile.store_district,
    store_zone: profile.store_zone,
    store_uf: profile.store_uf,
    store_zipcode: profile.store_zipcode
  });

  const { store_street = '', store_number = '', address_ref = '', store_district = '', store_zone = '', store_uf = '', store_zipcode = '' } = useInput;

  const isValid = () => store_zipcode.length === 9 && store_number && (store_street !== profile.store_street
  || store_number !== profile.store_number
  || address_ref !== profile.address_ref
  || store_district !== profile.store_district
  || store_zone !== profile.store_zone
  || store_uf !== profile.store_uf
  || store_zipcode !== profile.store_zipcode);

  const zipcodeFn = (val) => setInput((prevState) => Object({
    ...prevState,
    store_zipcode: val
  }));

  const numberFn = (val) => {
    if (!isNaN(Number(val)) && Number(val.replace(/[^0-9]/g, '')) !== store_number) {
      setInput((prevState) => Object({
        ...prevState,
        store_number: val
      }));
    };
  };

  const optionalFn = (val) => setInput((prevState) => Object({
    ...prevState,
    address_ref: val
  }));

  const cancelFn = () => {
    setShow(-1);
    setInput({
      store_street: profile.store_street,
      store_number: profile.store_number,
      address_ref: profile.address_ref,
      store_district: profile.store_district,
      store_zone: profile.store_zone,
      store_uf: profile.store_uf,
      store_zipcode: profile.store_zipcode
    });
  };

  const saveFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('updateBusiness')({ token, ref: 'profile', data: useInput })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!', Bracael.toast_options);
          setShow(-1);
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  async function viacepFn(val) {

    setBlock(true);
    setInput((prevState) => Object({
      ...prevState,
      store_street: 'Buscando...',
      store_district: 'Buscando...',
      store_zone: 'Buscando...',
      store_uf: 'Buscando...'
    }));

    await fetch(`https://viacep.com.br/ws/${val.replace(/\D/g, '')}/json/`, {
      method: 'GET',
      mode: 'cors',
      cache: 'default'
    })
    .then((response) => response.json())
    .then(function(result) {
      const { erro } = result;
      if (erro) {
        // PushAlert('CEP inválido!');
        setInput((prevState) => Object({
          ...prevState,
          store_street: '',
          store_district: '',
          store_zone: '',
          store_uf: ''
        }));
      }
      else {
        setInput((prevState) => Object({
          ...prevState,
          store_street: result.logradouro,
          store_district: result.bairro,
          store_zone: result.localidade,
          store_uf: result.uf
        }));
      };
      setBlock(false);
    }).catch(function() {
      setBlock(false);
    });
  };

  useEffect(() => {
    if (store_zipcode.length === 9 && store_zipcode !== profile.store_zipcode) {
      viacepFn(store_zipcode);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [store_zipcode])

  useEffect(() => {
    $(zipcodeRef.current).mask('00000-000');
  }, [zipcodeRef])

  return (
    <Content>
      <Name>Endereço</Name>
      <Main status={useShow === 5}>
        <Over>
          <Subname>{store_street}, {store_number}</Subname>
          <HREF type="button" onClick={() => setShow(5)}>Editar</HREF>
        </Over>
        <Under>
          <Value>
            <Input type="text" ref={zipcodeRef} onChange={(e) => !useBlock && zipcodeFn(e.target.value)} value={store_zipcode} />
          </Value>
          <Form>
            <Section>
              <Input type="text" readOnly value={store_street} minWidth="72%" maxWidth="100%" disabled />
              <Input type="text" onChange={(e) => numberFn(e.target.value)} value={store_number} maxWidth="100%" />
            </Section>
            <Section>
              <Input type="text" readOnly value={store_district} minWidth="72%" maxWidth="100%" disabled />
              <Input type="text" placeholder="Opcional" onChange={(e) => optionalFn(e.target.value)} value={address_ref} maxWidth="100%" />
            </Section>
            <Section>
              <Input type="text" readOnly value={store_zone} minWidth="72%" maxWidth="100%" disabled />
              <Input type="text" readOnly value={store_uf} maxWidth="100%" disabled />
            </Section>
          </Form>
          <Footer>
            <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Salvar alterações</Button>
            <Simple type="button" onClick={() => cancelFn()}>Cancelar</Simple>
          </Footer>
        </Under>
      </Main>
    </Content>
  );
}

export default ComponentBasicAddress;
