import React from 'react';

import { SnapshotAwait } from '../contexts/Gallery';

import { Main, Title, MSG, Footer, Simple, Button } from '../styles/ModalGallery';

function ComponentModalDeleteIMGMain({ close, name }) {

  const { setAwait } = SnapshotAwait();

  const removeFn = () => {
    setAwait({ name, status: true });
    close();
  };

  return (
    <Main>
      <Title>Remover imagem</Title>
      <MSG>Deseja mesmo remover esta imagem? Lembre-se que isso não poderá ser desfeito.</MSG>
      <Footer>
        <Simple type="button" onClick={() => close()}>Cancelar</Simple>
        <Button type="button" onClick={() => removeFn()}>Remover</Button>
      </Footer>
    </Main>
  )
}

export default ComponentModalDeleteIMGMain;
