import React, { useEffect, useRef, useState } from 'react';

import ElementProvider from '../contexts/ProductId';

import ComponentProductScreen from './ComponentProductScreen';
import ComponentOverload from './ComponentOverload';

const { jQuery: $ } = window;

function ComponentProductId({ data }) {

  const [usePost, setPost] = useState(data);
  const [useScreen, setScreen] = useState(1);
  const [useSection, setSection] = useState(1);

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [useModal, setModal] = useState({});

  useEffect(() => {
    const { status } = useModal;
    if (statusRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setModal({}));
    }
    else {
      statusRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal]);

  return (
    <ElementProvider {...{ usePost, setPost, useScreen, setScreen, useSection, setSection, useModal, setModal }}>
      <ComponentProductScreen />
      <ComponentOverload {...{ useModal, setModal, modalRef }} />
    </ElementProvider>
  );
};

export default ComponentProductId;
