import React, { useState } from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Content, Main, Text, Price, Values, Total } from '../styles/PriceOff';

function ComponentEditPriceOff() {

  const [useAnimate, setAnimate] = useState(false);
  const { usePost, setPost } = SnapshotPost();
  const { price = 0, price_off = 0 } = usePost;

  const inputFn = (val) => {
    if (!isNaN(Number(val)) && val.replace(/\D/g, '') !== String(price_off) && Number(val) < 100) {
      setPost((prevState) => Object({
        ...prevState,
        price_off: parseInt(Number(val))
      }));
    };
  };

  const altAmount = val => setPost((prevState) => Object({
    ...prevState,
    price_off: parseInt(Number(val))
  }));

  const altTransition = () => {
    setAnimate(true);
    setTimeout(() => setAnimate(false), 500);
  };

  const altDecrement = () => price_off > 0 ? altAmount(price_off-1) : altTransition();
  const altIncrement = () => price_off < 99 ? altAmount(price_off+1) : altTransition();

  return (
    <Content>
      <Text>Desconto com base em %</Text>
      <Main>
        <div>
          <Price >
            <span>Total:</span>
            <Total>{Number(price - price * price_off / 100).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Total>
          </Price >
        </div>
        <Values animeBtn={useAnimate}>
          <button type="button" onClick={() => altDecrement()}>-</button>
          <input type="text" onChange={(ev) => inputFn(ev.target.value)} value={price_off} />
          <button type="button" onClick={() => altIncrement()}>+</button>
        </Values>
      </Main>
    </Content>
  );
}

export default ComponentEditPriceOff;
