import React, { useRef, useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Mainly, Section, Name, Status, Label } from '../styles/Modify';

function ComponentPaymentCredit() {

  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { amex = false, diners = false, elo = false, hipercard = false, mastercard = false, visa = false } = useOutside.profile.methods.credit || {};

  const labelFn = (val, data) => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: `profile/methods/credit/${val}`, data })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (!status) {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  return (
    <Content>
      <Name>Cartão de Crédito</Name>
      <Mainly>
        <Section>
          <Status type="checkbox" readOnly checked={amex} />
          <Label onClick={() => labelFn('amex', !amex)}>AMERICAN EXPRESS</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={diners} />
          <Label onClick={() => labelFn('diners', !diners)}>DINERS CLUB</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={elo} />
          <Label onClick={() => labelFn('elo', !elo)}>ELO</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={hipercard} />
          <Label onClick={() => labelFn('hipercard', !hipercard)}>HIPERCARD</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={mastercard} />
          <Label onClick={() => labelFn('mastercard', !mastercard)}>MASTERCARD</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={visa} />
          <Label onClick={() => labelFn('visa', !visa)}>VISA</Label>
        </Section>
      </Mainly>
    </Content>
  );
}

export default ComponentPaymentCredit;
