import React from 'react';
import { useAsync } from "react-async";

import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';

import ComponentSettings from './ComponentSettings';
import ComponentSkeletonSettings from './ComponentSkeletonSettings';

const fetchPerson = async ({ token }, { signal }) => await functions()
  .httpsCallable('queryObject')({ token, ref: 'business' })
  .then((data) => data.data)

function ComponentAwaitSettings({ useModal, setModal }) {

  const { token } = SnapshotToken();
  const { data, error } = useAsync({ promiseFn: fetchPerson, token })

  if (error) return error.message
  if (data) return <ComponentSettings {...{ data, useModal, setModal }} />
  return <ComponentSkeletonSettings />
}

export default ComponentAwaitSettings;
