import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePost, setPost, useScreen, setScreen, useSection, setSection, useModal, setModal }) {

  return (
    <ElementContext.Provider value={{ usePost, setPost, useScreen, setScreen, useSection, setSection, useModal, setModal }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotPost() {
  const context = useContext(ElementContext)
  const { usePost, setPost } = context;
  return { usePost, setPost };
}

export function SnapshotScreen() {
  const context = useContext(ElementContext)
  const { useScreen, setScreen } = context;
  return { useScreen, setScreen };
}

export function SnapshotSection() {
  const context = useContext(ElementContext)
  const { useSection, setSection } = context;
  return { useSection, setSection };
}

export function SnapshotModal() {
  const context = useContext(ElementContext)
  const { useModal, setModal } = context;
  return { useModal, setModal };
}
