import React, { useState } from 'react';

import ComponentContainerAbout from './ComponentContainerAbout';
import ComponentContainerPolitics from './ComponentContainerPolitics';

import { Main } from '../styles/Settings';

function ComponentSettingsContainer() {

  const [useShow, setShow] = useState(-1);

  return (
    <Main>
      <ComponentContainerAbout {...{ useShow, setShow }} />
      <ComponentContainerPolitics {...{ useShow, setShow }} />
    </Main>
  );
}

export default ComponentSettingsContainer;
