
module.exports = {
  u: {
    a: ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"],
    b: ["Domingo", "Segunda", "Terça", "Quarta", "Quinta", "Sexta", "Sábado"],
    c: ["Domingo", "Segunda-feira", "Terça-feira", "Quarta-feira", "Quinta-feira", "Sexta-feira", "Sábado"],
    d: ["sun", "mon", "tue", "wed", "thu", "fri", "sat"],
    e: new Date().getDay()
  },
  toast_options: {
    position: "top-right",
    autoClose: 3000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined
  },
  category_names: [{
    keycode: '001',
    keyname: 'Hambúrguer'
  }, {
    keycode: '002',
    keyname: 'Porções'
  }, {
    keycode: '003',
    keyname: 'Bebidas'
  }, {
    keycode: '004',
    keyname: 'Combos'
  }, {
    keycode: '005',
    keyname: 'Promoções'
  }, {
    keycode: '006',
    keyname: 'Bomboniere'
  }],
  post_status: [{
    status: 200,
    name: 'Disponível'
  }, {
    status: 403,
    name: 'Indisponível'
  }, {
    status: 203,
    name: 'Rascunho'
  }],
  order_status: [{
    status: '001',
    name: 'Em espera'
  }, {
    status: '002',
    name: 'Em preparo'
  }, {
    status: '003',
    name: 'Em retirada'
  }],
  cupom_names: {
    '001': {
      name: 'Desconto em porcentagem'
    },
    '002': {
      name: 'Desconto em valor fixo'
    },
    '003': {
      name: 'Desconto para produto'
    }
  },
  subtotal: function ({ detail, feed }) {
    return parseFloat((detail
    .map(function (assoc) {
      const { id, amount = 1, additional = {} } = assoc;
      const { price, extra = {}, price_off = 0 } = feed[id] || {};

      return Number((amount * (price - price * price_off / 100)) + Object.entries(extra)
      .filter((data) => Object.keys(additional).includes(data[0]))
      .map((data) => {
        const uid = data[0];
        const { value } = data[1];
        const { amount = 1 } = additional[uid] || {};

        return Number((value * amount))
      }).reduce((a, b) => a + b, 0))
    }).reduce((a, b) => a + b, 0)).toFixed(2))
  },
  formatOrder: function (data) {

    const { profile, cupons, delivery, order, feed } = data;
    const span = '```';
    // eslint-disable-next-line no-unused-vars
    const { whatsapp } = profile;

    const { comment = '', display_name, street, telephone_number, delivery_select, detail, house_number, complement, trade = 0, payment = {}, order_type, cupom_promotion } = order;
    const { district, rate = 0, zone } = order_type !== 2 ? delivery[delivery_select] : {};
    const { type_off = '001', value = 0 } = cupons[cupom_promotion] || {};

    const totalCart = type_off === '001'
    ? parseFloat((this.subtotal({ detail, feed }) - this.subtotal({ detail, feed }) * value / 100).toFixed(2))
    : parseFloat((this.subtotal({ detail, feed }) + ~value+1).toFixed(2));

    const comandCart = detail
    .map((assoc) => {
      const { id, amount = 1, comment, additional = {}, selector = {} } = assoc;
      const { title, weight, grams, extra = {}, pick = {} } = Object.entries(feed).find((data) => data[0] === id)[1];

      const lineComment = comment ? `\n└─ *“${comment}”*` : '';
      const lineExtra = Object.keys(extra).length ? Object.entries(extra)
      .filter(val => Object.keys(additional).includes(val[0]))
      .map((val, i, all) => {

        const uid = val[0];
        const { name } = val[1];
        const { amount = 1 } = additional[uid] || {};

        return `\n${(i === all.length - 1 && !lineComment) ? '└─' : '├─'} _${amount} extra de ${name}_`

      })
      .join('') : '';

      const lineSelector = Object.keys(selector).length ?
      Object.entries(selector)
      .map((val) => {
        const [id, values] = val;
        const { name, important, limit } = pick[id];

        return `\n├─ _${name}_${values.map((data, i) => `\n├─── _${important ? `${i+1} de ${limit} ` : ''}${data}_`).join('')}`
      }).join('') : '';

      return `*${amount}x* ${span}${title}${weight ? this.unitSize({ ml: weight, space: true }) : grams ? this.unitSize({ gm: grams, space: true }) : ''}${span}${lineSelector}${lineExtra}${lineComment}`;
    });

    const formatMethods = () => {
      const { cash = false, pix = false, debit = [], credit = [], voucher = [] } = payment;
      const user_method = [];
      cash && user_method.push('Dinheiro');
      pix && user_method.push('Pix');
      debit.length && user_method.push('Débito');
      credit.length && user_method.push('Crédito');
      voucher.length && user_method.push('Voucher');

      return `${user_method.join(', ').replace(/, ([^,]*)$/, ' e $1')}`;
    };

    return `*# DADOS PESSOAIS*
👤 _Nome:_ ${span}${display_name}${span}
📞 _Telefone:_ ${span}${telephone_number}${span}
${order_type !== 2
? `🌎 _Endereço:_ ${span}${street}, ${house_number}${complement ? `, ${complement}` : ''} - ${zone}${district ? ` - ${district}` : ''}${span}`
: `🥡 _Endereço:_ ${span}Retirar no local${span}`}


*# SOBRE O PEDIDO*
${comandCart.join('\n')}

*# SOBRE O PAGAMENTO*${comment ? `\n💬 _Comentário:_ ${comment}` : ''}
${`🤝🏻 _Forma de pagamento:_ ${formatMethods()}`}
${trade ? `💰 _Troco para:_ ${trade.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n` : ''}${`💰 _Subtotal:_ ${this.subtotal({ detail, feed }).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n`}${order_type !== 2 ? `💰 _Taxa de entrega:_ ${rate.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}\n` : ''}${value ? `💰 _Desconto:_ ${type_off !== '001'
? Number(~value+1).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
: Number(-Math.abs(this.subtotal({ detail, feed }) - totalCart)).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })} (${value}% OFF)\n` : ''}💰 *_TOTAL:_* ${span}${Number(totalCart + rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}${span}`

  },
  isValid: function (data) {

    const { category, thumb = [], description, weight, price, title, available = {} } = data;
    const broken = (val) => !!val;

    return broken(this.category_names.map((data) => data.keycode).includes(category))
    && broken(thumb.length)
    && broken(title)
    && broken((category === '003') ? true : description)
    && broken((category === '003') ? weight : true)
    && broken(price)
    && broken((category === '005' ? Object.values(available).includes(true) : true))
  },
  unitSize: ({ weight, grams, space = false, str = false }) => {

    const sizeGrams = {
      name: 'kilos',
      bigger: 'kg',
      smaller: 'g',
      size: grams
    }

    const sizeWeight = {
      name: 'litros',
      bigger: 'l',
      smaller: 'ml',
      size: weight
    }

    const Measure = ({ name, bigger, smaller, size }) => {

      var countGrams = 0;
      var atual = size;

      const decrementAgain = () => {
        countGrams = ++countGrams;
        atual = atual - 1000
        // eslint-disable-next-line no-unused-expressions
        atual >= 1000 ? decrementAgain() : null;
      }

      // inicia um contador para verificar se final é menor que para enviar ao separador
      (atual >= 1000) && decrementAgain();

      // somente para encurtar o valor e tornar o código legível
      const toUp = bigger.toUpperCase()

      // o valor atual decide se irá encurtar o valor recebido. Ex.: 2,5L -- 1KG
      const returnSize = () => atual ?
        `${Number(countGrams + '.' + atual).toFixed(1).replace('.', ',')}${toUp}` :
        `${countGrams}${toUp}`

      // verifica se o valor é maior ou menos que 1000 e decide se é (litros o ml / kilos ou gramas)
      return size >= 1000 ?
        `${space ? ' ' : ''}${returnSize()}` :
        `${space ? ' ' : ''}${size}${smaller}`
    }

    return (typeof weight === 'number' || typeof grams === 'number')
      ? weight ? Measure(sizeWeight) : Measure(sizeGrams) : '';
  }
}
