import React, { useState } from 'react';

import Bracael from '../Bracael';

import ComponentMapLive from './ComponentMapLive';

import { Main } from '../styles/Settings';

function ComponentSettingsLive() {

  const [useShow, setShow] = useState(-1);

  return (
    <Main>
      {Bracael.u.d.map((data, i) => <ComponentMapLive key={i} {...{ data, i, useShow, setShow }} />)}
    </Main>
  );
}

export default ComponentSettingsLive;
