import React, { useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { SnapshotPost, SnapshotSection } from '../contexts/ProductId';

import ComponentMapAdditional from './ComponentMapAdditional';

import { Content, Contently, Main, Footer, Simple, Button } from '../styles/Additional';

function ComponentPluginsAdditional() {

  const start_empty = { moment: new Date().toISOString() };
  const start_li = { [parseInt(Math.random() * 1000000000, 10)]: start_empty };

  // eslint-disable-next-line no-unused-vars
  const { useSection, setSection } = SnapshotSection();
  const { usePost, setPost } = SnapshotPost();
  const { extra = {} } = usePost;
  const [useExtra, setExtra] = useState(Object.keys(extra).length ? extra : start_li);

  const organize = (a, b) => (a[1].moment > b[1].moment) ? 1 : ((b[1].moment > a[1].moment) ? -1 : 0);

  const isValidNew = (data) => {
    const [, assoc] = data;
    const { name, limit, value } = assoc;
    return !!(name && limit && value);
  };
  const newFn = () => {
    if (Object.entries(useExtra).map(isValidNew).includes(false)) {
      toast.info('Termine o item atual', Bracael.toast_options);
    }
    else {
      toast('Adicional novo!');
      const assoc = {...useExtra};
      assoc[parseInt(Math.random() * 1000000000, 10)] = start_empty;
      setExtra(assoc);
    };
  };

  const saveFn = () => {
    if(Object.entries(useExtra).map(isValidNew).every(val => !!val)) {
      toast.success('Adicionais atualizados!', Bracael.toast_options);
      setSection(1);
      setPost((prevState) => Object({
        ...prevState,
        extra: useExtra
      }));
    }
    else {
      toast.error('Preencha todos os campos', Bracael.toast_options);
    }
  };

  const testStatus = (input, output) => {

    return Object.entries(input).map((data, i) => {
      const [id = i, assoc] = data;
      const { about = '', limit = 1, name = '', value = 0 } = output[id] || {};

      return ((assoc.about || false) === about)
      && ((assoc.limit || 1) === limit)
      && ((assoc.name || '') === name)
      && ((assoc.value || '') === value)
    }).includes(false);
  };

  const isValid = () => {

    return Object.keys(useExtra).length < Object.keys(extra).length
    ? testStatus(extra, useExtra)
    : testStatus(useExtra, extra);
  };

  return (
    <Content>
      <Contently>
        <Main>
          {Object.entries(useExtra)
          .sort(organize)
          .map((data, i) => <ComponentMapAdditional key={data[0]} {...{ data, i, useExtra, setExtra }} />)}
        </Main>
        <Footer>
          <Simple type="button" onClick={() => newFn()}>Novo adicional</Simple>
          <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Confirmar</Button>
        </Footer>
      </Contently>
    </Content>
  )
}

export default ComponentPluginsAdditional;
