import React, { useRef, useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Mainly, Name, Status, Label } from '../styles/Modify';

function ComponentBasicStatus() {

  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { status = false } = useOutside.profile || {};

  const labelFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: 'profile/status', data: !status })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (!status) {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  return (
    <Content>
      <Name>Status</Name>
      <Mainly>
        <Status type="checkbox" readOnly checked={status} />
        <Label onClick={() => labelFn()}>{status ? 'Aberto' : 'Fechado'}</Label>
      </Mainly>
    </Content>
  );
}

export default ComponentBasicStatus;
