import React from 'react';

import { Content, Icon, Name, Status, Size } from '../styles/Progress';

function ComponentUploadSucess({ data }) {

  const { file } = data;
  const { name, size } = file;

  const nameSplit = [...name.split('.')];
  let popped = nameSplit.pop();

  return (
    <Content>
      <Icon>
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="#a067e6"><path d="M7,8.5C7,7.119,8.119,6,9.5,6S12,7.119,12,8.5S10.881,11,9.5,11S7,9.881,7,8.5z M14.5,11l-4,6l-2-3L5,19h15L14.5,11z" /></svg>
      </Icon>
      <Name>{name}</Name>
      <Size>{popped.toUpperCase()} / {(size < 1000000) ? Math.floor(size/1000) + ' KB' : Math.floor(size/1000000) + ' MB'}</Size>
      <Status typeStatus="sucess">Concluído</Status>
    </Content>
  );
}

export default ComponentUploadSucess;
