import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Group, Input, Label } from '../styles/Post';

function ComponentEditGrams() {

  const { usePost, setPost } = SnapshotPost();
  const { grams = 0 } = usePost;

  const altTitle = val => {
    if (!isNaN(Number(val)) && Number(val.replace(/[^0-9]/g, '')) !== grams) {
      setPost((prevState) => Object({
        ...prevState,
        grams: Number(val)
      }));
    };
  };

  return (
    <Group>
      <Input type="text" placeholder="0"
        onInput={(e) => altTitle(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={grams || ''}
        status={true} />
      <Label>Gramas</Label>
    </Group>
  );
}

export default ComponentEditGrams;
