import React from 'react';
import { useAsync } from 'react-async';

import { getImageUrl } from '../app/';

import { storage } from '../services/server';
import { SnapshotWhere } from '../contexts/App';
import { SnapshotPost } from '../contexts/ProductId';

import ComponentSkeletonEditThumb from './ComponentSkeletonEditThumb';
import ComponentEditSwipeable from './ComponentEditSwipeable';

async function processFn(src){
  const img = new Image();
  img.src = src;
  await img.decode();
  return img.src
};

const requestFn = async (where, data) => {

  async function sucessFn(snapshot) {
    const { fullPath } = snapshot;
    return {
      assoc: snapshot,
      url: await storage()
        .ref(fullPath)
        .getDownloadURL()
        .then(processFn)
    };
  };

  return await Promise.all(data.map(async (data) => {
    const { name, moment } = data;

    return await storage()
    .ref(getImageUrl({ where, ref: `${name + '_160x160.jpeg'}` }))
    .getMetadata()
    .catch((error) => Object({ error: true, moment, name }));
  }))
  .then((snapshot) => Promise.all(snapshot.map((data) => data.error ? data : sucessFn(data))));
};

const fetchPerson = async ({ useWhere, thumb }, { signal }) => await requestFn(useWhere, thumb);

function ComponentAwaitEditThumb() {

  const { usePost } = SnapshotPost();
  const { thumb = [] } = usePost;
  const { useWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, thumb });

  if (error) return error.message
  if (data) return <ComponentEditSwipeable {...{ data }} />
  return <ComponentSkeletonEditThumb />
}

export default ComponentAwaitEditThumb;
