import React, { useEffect, useRef } from 'react';

import { SnapshotFilter } from '../contexts/ListProducts';

import ComponentFilterMain from './ComponentFilterMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalFilter() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useFilter, setFilter } = SnapshotFilter();

  useEffect(() => {
    const { status } = useFilter;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useFilter]);

  useEffect(() => {
    const { status } = useFilter;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setFilter({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useFilter]);

  const cancenlFn = () => setFilter((prevState) => Object({ status: false }));

  return (
    <Modal ref={modalRef} zIndex="6">
      {(typeof (useFilter?.status || false) === "boolean") ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentFilterMain close={cancenlFn} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalFilter;
