import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Name, Main, Over, Under, Value, Input, Subname, HREF, Footer, Simple, Button } from '../styles/Modify';

const { jQuery: $ } = window;

function ComponentBasicTel({ useShow, setShow }) {

  const inputRef = useRef(null);
  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { whatsapp = '' } = useOutside.profile || {};
  const [useInput, setInput] = useState(whatsapp);

  const isValid = () => (useInput !== whatsapp) && (useInput.replace(/[^0-9]/g, '').length === 10 || useInput.replace(/[^0-9]/g, '').length === 11);
  const inputFn = (val) => setInput(val);
  const cancelFn = () => {
    setInput(whatsapp);
    setShow(-1);
  };
  const saveFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: 'profile/whatsapp', data: useInput })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!', Bracael.toast_options);
          setShow(-1);
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  useEffect(() => {
    var SPMaskBehavior = function (val) {
      return val.replace(/\D/g, '').length === 11 ? '(00) 00000-0000' : '(00) 0000-00009';
    },
      spOptions = {
        onKeyPress: function (val, e, field, options) {
          field.mask(SPMaskBehavior.apply({}, arguments), options);
        }
      };

    $(inputRef.current).mask(SPMaskBehavior, spOptions);
  }, [inputRef])

  return (
    <Content>
      <Name>WhatsApp</Name>
      <Main status={useShow === 3}>
        <Over>
          <Subname>{whatsapp}</Subname>
          <HREF type="button" onClick={() => setShow(3)}>Editar</HREF>
        </Over>
        <Under>
          <Value>
            <Input type="text" ref={inputRef} onChange={(e) => inputFn(e.target.value)} value={useInput} />
          </Value>
          <Footer>
            <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Salvar alterações</Button>
            <Simple type="button" onClick={() => cancelFn()}>Cancelar</Simple>
          </Footer>
        </Under>
      </Main>
    </Content>
  );
}

export default ComponentBasicTel;
