import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotUpdate } from '../contexts/Products';
import { SnapshotBar, SnapshotData, SnapshotDraftOff } from '../contexts/ListProducts';

import ComponentFormModalSimple from './ComponentFormModalSimple';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalDraftOn() {

  const clickRef = useRef(true);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { setUpdate } = SnapshotUpdate();
  const { setBar } = SnapshotBar();
  const [useAttempts, setAttempts] = useState(0);

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useDraftOff, setDraftOff } = SnapshotDraftOff();
  const { id } = useDraftOff;
  const { data } = SnapshotData();

  useEffect(() => {
    const { status } = useDraftOff;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useDraftOff]);

  useEffect(() => {
    const { status } = useDraftOff;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setDraftOff({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useDraftOff]);

  const cancenlFn = () => setDraftOff((prevState) => Object({ ...prevState, status: false }));

  const valuesForm = {
    title: 'Exibir produto?',
    message: 'Isso fará com que este produto esteja disponível a compra em sua loja. Você quer mesmo continuar?',
    buttons: [{ value: "Cancelar", status: true }, { value: "Confirmar", status: true }],
    close: cancenlFn,
    confirmFn: function() {
      if(clickRef.current) {
        clickRef.current = false;
        setModal({ status: true });

        functions()
        .httpsCallable('updateStatus')({ token, id, data: 200 })
        .then((data) => data.data)
        .then(function(data) {
          const { status } = data;
          if (status) {
            setUpdate((prevState) => prevState+1);
            setModal({ status: false });
            toast.success('Configurações salvas!', Bracael.toast_options);
          }
          else {
            if (useAttempts < 3) {
              toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
              setAttempts((prevState) => prevState+1);
            }
            else {
              toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
            };

            setBar('');
          };
        })
        .catch(function() {
          toast.error('Erro na chamada API', Bracael.toast_options);
        })
        .finally(function() {
          clickRef.current = true;
          setModal({ status: false });
        });
      };
    }
  };

  return (
    <Modal ref={modalRef} zIndex="6">
      {data[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentFormModalSimple {...{ valuesForm }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalDraftOn;
