import React, { useState, useEffect, useRef } from 'react';
import { useParams } from 'react-router';
import { useHistory } from 'react-router';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotPost, SnapshotModal } from '../contexts/ProductId';

import ComponentProductMain from './ComponentProductMain';

import { Content, Menu, Name, Category, P, Buttons, Go, Simple } from '../styles/Post';

function ComponentProductEdit() {

  const { id } = useParams();
  const history = useHistory();
  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { setModal } = SnapshotModal();
  const [useRoute, setRoute] = useState(false);
  const { token } = SnapshotToken();
  const { usePost } = SnapshotPost();
  const { category } = usePost;
  const { keyname } = Bracael.category_names.find((data) => data.keycode === category) || {};

  useEffect(() => {
    if (useRoute) {
      history.push('/products');
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useRoute]);

  const updateFn = () => {
    if (clickRef.current) {
      clickRef.current = false;

      if (Bracael.isValid(usePost)) {
        setModal({ status: true });
        functions().httpsCallable('updateProduct')({
          token,
          id,
          data: {
            ...usePost,
            update: new Date().toISOString()
          }
        })
        .then((data) => data.data)
        .then(function(data) {
          const { status } = data;
          if (status) {
            toast.success('Produto atualizado com sucesso!', Bracael.toast_options);
            setRoute(true);
          }
          else {
            if (useAttempts < 3) {
              toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
              setAttempts((prevState) => prevState+1);
            }
            else {
              toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
            };

          };
        })
        .catch(function (error) {
          toast.error('Servidor não encontrado!', Bracael.toast_options);
        })
        .finally(function() {
          setModal({ status: false });
          clickRef.current = true;
        });
      }
      else {
        clickRef.current = true;
        toast.error('Há algo de errado!', Bracael.toast_options);
      };
    };
  };

  return (
    <Content>
      <Menu>
        <Name>
          <Category>{keyname}</Category>
          <P>Você está publicando para {keyname}</P>
        </Name>
        <Buttons>
          <Go type="button" onClick={() => updateFn()}>Atualizar</Go>
          <Simple type="button" onClick={() => history.push('/products')}>Fechar</Simple>
        </Buttons>
      </Menu>
      <ComponentProductMain />
    </Content>
  );
}

export default ComponentProductEdit;
