import React, { useEffect, useRef, useState } from 'react';

import ElementProvider from '../contexts/ProductId';

import ComponentProductPost from '../components/ComponentProductPost';
import ComponentHeader from '../components/ComponentHeader';
import ComponentOverload from '../components/ComponentOverload';

import { Scroll } from '../styles/root';

const { jQuery: $ } = window;

function CreateProduct() {

  const configGlobal = {
    name_content: "Editar novo produto"
  };

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [usePost, setPost] = useState({});
  const [useScreen, setScreen] = useState(1);
  const [useSection, setSection] = useState(1);
  const [useModal, setModal] = useState({});

  useEffect(() => {
    const { status } = useModal;
    if (statusRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setModal({}));
    }
    else {
      statusRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal]);

  return (
    <>
      <ComponentHeader {...{ configGlobal }} />
      <Scroll>
        <ElementProvider {...{ usePost, setPost, useScreen, setScreen, useSection, setSection, useModal, setModal }}>
          <ComponentProductPost />
        </ElementProvider>
        <ComponentOverload {...{ useModal, setModal, modalRef }} />
      </Scroll>
    </>
  );
}

export default CreateProduct;
