import React, { useEffect, useRef } from 'react';

import { SnapshotOrder, SnapshotAccept } from '../contexts/Service';

import ComponentAcceptMain from './ComponentAcceptMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalAccept() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useAccept, setAccept } = SnapshotAccept();
  const { useOrder } = SnapshotOrder();
  const { id } = useAccept;

  useEffect(() => {
    const { status } = useAccept;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useAccept]);

  useEffect(() => {
    const { status } = useAccept;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setAccept({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useAccept]);

  const cancenlFn = () => setAccept((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} zIndex="6">
      {useOrder[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentAcceptMain close={cancenlFn} {...{ id }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalAccept;
