import React from 'react';

import ComponentBasicBreadcrumb from './ComponentBasicBreadcrumb';
import ComponentPaymentBreadcrumb from './ComponentPaymentBreadcrumb';
import ComponentFreteBreadcrumb from './ComponentFreteBreadcrumb';
import ComponentLiveBreadcrumb from './ComponentLiveBreadcrumb';
import ComponentContainerBreadcrumb from './ComponentContainerBreadcrumb';
import ComponentContainerAccessibility from './ComponentContainerAccessibility';
import ComponentCupomBreadcrumb from './ComponentCupomBreadcrumb';
import ComponentCuponsBreadcrumb from './ComponentCuponsBreadcrumb';

function ComponentSettingsBreadcrumb({ useSection, setSection }) {

  if (useSection === 1) return <ComponentBasicBreadcrumb {...{ useSection, setSection }} />
  if (useSection === 2) return <ComponentPaymentBreadcrumb {...{ useSection, setSection }} />
  if (useSection === 3) return <ComponentFreteBreadcrumb {...{ useSection, setSection }} />
  if (useSection === 4) return <ComponentLiveBreadcrumb {...{ useSection, setSection }} />
  if (useSection === 5) return <ComponentContainerBreadcrumb {...{ useSection, setSection }} />
  if (useSection === 6) return <ComponentContainerAccessibility />
  if (useSection === 7) return <ComponentCupomBreadcrumb />
  if (useSection === 8) return <ComponentCuponsBreadcrumb />
  return null;
}

export default ComponentSettingsBreadcrumb;
