import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';
import { useHistory } from 'react-router-dom';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal } from '../contexts/Settings';
import ElementProvider from '../contexts/Cupom';

import { Main } from '../styles/Settings';
import { Content, Flex, Code, SPAN, Group, Select, Input, Label, Divlabel, Sublabel, Endlabel, Footer, Button } from '../styles/Cupom';

const { jQuery: $ } = window;

function ComponentSettingsCupom() {

  const history = useHistory();

  const dateRef = useRef(null);
  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { useModal, setModal } = SnapshotModal();

  const [useCode, setCode] = useState('');
  const [useInput, setInput] = useState({ moment: new Date().toISOString() });
  const { title = '', value = 0, max_use = 0, min_value = 0, max_value = 0, validate = '' } = useInput;

  const isValidCode = (val) => {
    return /^[a-zA-Z]{1}[\w]*$/.test(val.toUpperCase()) || val === ''
  };

  const isValidFullFormat = (val) => {
    return /[\d]{4}[-][\d]{2}[-][\d]{2}/.test(val.toUpperCase())
  };

  const isValidYear = (val) => {
    return String(new Date().getFullYear()).split('')
    .filter((data, i) => i <= val.substring(0,4).length-1)
    .map((data, i) => Number(data) <= val.substring(0,4)[i])
    .every((data) => !!data);
  };

  const isValidMonth = (val) => {
    return Number(val.substr(5,2)) <= 12
    && !isNaN(Number(val.substr(5,2)))
  };

  const isValidDay = (val) => {
    return Number(val.substr(8,2)) <= 31
    && !isNaN(Number(val.substr(5,2)))
  };

  const dateFn = (val) => {
    if ((
      isValidYear(val)
      && isValidMonth(val)
      && isValidDay(val)
    ) || !val) {
      setInput((prevState) => Object({
        ...prevState,
        validate: val
      }));
    }
    else {
      if (!isValidYear(val)) {
        toast.warn('Digite um ano válido', Bracael.toast_options);
      }
      else if (!isValidMonth(val)) {
        toast.warn('Digite um mês válido', Bracael.toast_options);
      }
      else if (!isValidDay(val)) {
        toast.warn('Digite um dia válido', Bracael.toast_options);
      }
      else {
        toast.error('Formato de data inválido!', Bracael.toast_options);
      };
    };
  };

  const codeFn = (val) => {
    if (isValidCode(val)) {
      setCode(val.toUpperCase().trim());
    };
  };

  const inputFn = ({ ref, val }) => {
    setInput((prevState) => Object({
      ...prevState,
      [ref]: val
    }));
  };

  const numberFn = ({ ref, val }) => {
    if (!isNaN(Number(val)) && val.replace(/\D/g, '') !== String(useInput[ref] || '')) {
      setInput((prevState) => Object({
        ...prevState,
        [ref]: Number(val)
      }));
    };
  };

  const isValid = () => {
    return useCode && (validate ? isValidFullFormat(validate) : true) && title && value;
  };

  const confirmFn = () => {
    if (isValid()) {
      if(clickRef.current) {
        clickRef.current = false;
        setModal({ status: true });

        functions()
        .httpsCallable('setCupom')({ token, code: useCode, data: useInput })
        .then((data) => data.data)
        .then(function(data) {
          console.log(data);

          const { status } = data;

          if (status === 200) {
            history.push('?section=accessibility');
            toast.success('Cupom criado com sucesso!', Bracael.toast_options);
          }
          else if (status === 404) {
            toast.error('Este código já existe!', Bracael.toast_options);
          }
          else {
            if (useAttempts < 3) {
              toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
              setAttempts((prevState) => prevState+1);
            }
            else {
              toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
            };
          };
        })
        .catch(function() {
          toast.error('Erro na chamada API', Bracael.toast_options);
        })
        .finally(function() {
          clickRef.current = true;
          setModal({ status: false });
        });
      };
    }
    else {
      if(!useCode) {
        toast.warn('Digite um código', Bracael.toast_options);
      }
      else if(!title) {
        toast.warn('Digite um título', Bracael.toast_options);
      }
      else if(!value) {
        toast.warn('Digite um valor para o cupom', Bracael.toast_options);
      }
      else if(validate && !isValidFullFormat(validate)) {
        toast.warn('Informe uma data', Bracael.toast_options);
      }
      else {
        toast.error('Preencha todos os campos!', Bracael.toast_options);
      };
    };
  };

  const selectFn = (event) => {
    setInput((prevState) => Object({
      ...prevState,
      type_off: event.options[event.selectedIndex].value
    }));
  };

  useEffect(() => {
    $(dateRef.current).mask('0000-00-00');
  }, [dateRef]);

  return (
    <ElementProvider {...{ useInput, setInput }}>
      <Main>
        <Content>
          <Flex>
            <Code type="text" placeholder="Código do cupom" onChange={(e) => codeFn(e.target.value)} value={useCode} />
            <SPAN>Seu código – Insira qualquer código alfanumérico que desejar iniciando com uma letra.</SPAN>
          </Flex>
          <Flex>
            <Label>Tipo de desconto</Label>
            <Select onChange={(e) => selectFn(e.target)}>
              <option value="001">Desconto em porcentagem</option>
              <option value="002">Desconto em valor específico</option>
            </Select>
          </Flex>
          <Group>
            <Input type="text" placeholder="Ex.: Black Friday" minWidth="72%" maxWidth="100%"
            onChange={(e) => inputFn({ ref: 'title', val: e.target.value })}
            value={title} status={title} />
            <Label>Título</Label>
          </Group>
          <Group>
            <Input type="text" placeholder="AAAA-MM-DD" minWidth="72%" maxWidth="100%"
            ref={dateRef} onChange={(e) => dateFn(e.target.value)}
            value={validate} status={true} />
            <Label>
              <span>Data de expiração</span>
              <Divlabel>
                <Endlabel>(</Endlabel>
                <Sublabel>opcional</Sublabel>
                <Endlabel>)</Endlabel>
              </Divlabel>
            </Label>
          </Group>
          <Group>
            <Input type="text" placeholder="0"
            minWidth="72%" maxWidth="100%"
            onInput={(e) => numberFn({ ref: 'value', val: e.target.value })}
            autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off"
            value={String(value || '')} status={value} />
            <Label>
              <span>Valor do cupom</span>
            </Label>
          </Group>
          <Group>
            <Input type="text" placeholder="Uso ilimitado" minWidth="72%" maxWidth="100%"
            onInput={(e) => numberFn({ ref: 'max_use', val: e.target.value })}
            autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off"
            value={(max_use || '')} status={max_use} />
            <Label>
              <span>Limite de uso</span>
              <Divlabel>
                <Endlabel>(</Endlabel>
                <Sublabel>opcional</Sublabel>
                <Endlabel>)</Endlabel>
              </Divlabel>
            </Label>
          </Group>
          <Group>
            <Input type="text" placeholder="Nenhum máximo" minWidth="72%" maxWidth="100%"
            onInput={(e) => numberFn({ ref: 'max_value', val: e.target.value })}
            autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off"
            value={(max_value || '')} status={max_value} />
            <Label>
              <span>Gasto máximo</span>
              <Divlabel>
                <Endlabel>(</Endlabel>
                <Sublabel>opcional</Sublabel>
                <Endlabel>)</Endlabel>
              </Divlabel>
            </Label>
          </Group>
          <Group>
            <Input type="text" placeholder="Nenhum valor mínimo" minWidth="72%" maxWidth="100%"
            onInput={(e) => numberFn({ ref: 'min_value', val: e.target.value })}
            autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off"
            value={(min_value || '')} status={max_value} />
            <Label>
              <span>Gasto mínimo</span>
              <Divlabel>
                <Endlabel>(</Endlabel>
                <Sublabel>opcional</Sublabel>
                <Endlabel>)</Endlabel>
              </Divlabel>
            </Label>
          </Group>
        </Content>
        <Footer>
          <Button type="button" status={useModal.status} onClick={() => confirmFn()}>
            {useModal.status
            ? (<div className="stage">
                <div className="dot-falling"></div>
              </div>)
            : <span>Confirmar cupom</span>}
          </Button>
        </Footer>
      </Main>
    </ElementProvider>
  );
}

export default ComponentSettingsCupom;
