import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useInput, setInput }) {

  return (
    <ElementContext.Provider value={{ useInput, setInput }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotInput() {
  const context = useContext(ElementContext)
  const { useInput, setInput } = context;
  return { useInput, setInput };
}
