import React, { useState, useEffect, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Name, Main, Over, Under, Value, Input, Subname, HREF, Footer, Simple, Button } from '../styles/Modify';

const { jQuery: $ } = window;

function ComponentBasicPrice({ useShow, setShow }) {

  const inputRef = useRef(null);
  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { min_value = 0 } = useOutside.profile || {};
  const [useInput, setInput] = useState(min_value ? min_value.toFixed(2) : '');

  const formatNumber = () => isNaN(Number(useInput))
  ? Number(useInput.replace(/\./g, '').replace(/,/g, '.'))
  : Number(useInput);
  const isValid = () => formatNumber() !== min_value;
  const inputFn = (val) => setInput(val);
  const cancelFn = () => {
    setInput(min_value);
    setShow(-1);
  };

  const saveFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: 'profile/min_value', data: formatNumber() })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!', Bracael.toast_options);
          setShow(-1);
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  useEffect(() => {
    inputRef.current.value = useInput;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useInput]);

  useEffect(() => {
    $(inputRef.current).mask('#.##0,00', { reverse: true });
  }, [inputRef]);

  return (
    <Content>
      <Name>Preço minimo</Name>
      <Main status={useShow === 6}>
        <Over>
          <Subname>{Number(min_value).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Subname>
          <HREF type="button" onClick={() => setShow(6)}>Editar</HREF>
        </Over>
        <Under>
          <Value>
            <Input type="text" placeholder="0,00" ref={inputRef} onInput={(e) => inputFn(e.target.value)} autoCapitalize="off" autoCorrect="off"
            onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
            onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
            onDrop={(e) => e.preventDefault()} autoComplete="off" />
          </Value>
          <Footer>
            <Button type="button" status={isValid()} onClick={() => isValid() && saveFn()}>Salvar alterações</Button>
            <Simple type="button" onClick={() => cancelFn()}>Cancelar</Simple>
          </Footer>
        </Under>
      </Main>
    </Content>
  );
}

export default ComponentBasicPrice;
