export const Status = styled.input`

  &:checked ~ label::before {
    color: #fff;
    border-color: rgba(25, 145, 107, 0.3);
    background-color: rgba(25, 145, 107, 0.6);
  }

  &:checked ~ label::after {
    background-color: #fff;
    -webkit-transform: translateX(2.2rem);
    transform: translateX(2.2rem);
  }

  & ~ label::after {
    background-color: #aebcca;
    border-radius: 1.5rem;
    -webkit-transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
    transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;
    transition: transform 0.15s ease-in-out,background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out,-webkit-transform 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
  }

  &:not(:disabled):active ~ label::before {
    color: #fff;
    border-color: #75e7c3;
    background-color: #75e7c3;
  }

  &:focus:not(:checked) ~ label::before {
    border-color: #4ae0b0;
  }

  &:focus ~ label::before {
    -webkit-box-shadow: 0 0 0 0.2rem rgba(25,145,107,0.25);
    box-shadow: 0 0 0 0.2rem rgba(25,145,107,0.25);
  }
`

export const Label = styled.label`
  color: rgba(0, 0, 0, 0.65);
  font-weight: 700;
  font-family: var(--font-lato);
  font-style: italic;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  float: left;

  &::before {
    pointer-events: all;
    border-radius: 1.8rem;
    margin-right: 1.2rem;
    float: left;
  }

  &::before {
    -webkit-transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
    transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
    transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out;
    transition: background-color 0.15s ease-in-out,border-color 0.15s ease-in-out,box-shadow 0.15s ease-in-out,-webkit-box-shadow 0.15s ease-in-out;
  }

  &::before {
    display: block;
    width: 5rem;
    height: 2.4rem;
    pointer-events: none;
    content: "";
    background-color: #fff;
    border: #aebcca solid 1px;
  }

  &::after {
    position: absolute;
    top: 0.4rem;
    left: .4rem;
    display: block;
    width: 2rem;
    height: 1.6rem;
    content: "";
    background: no-repeat 50% / 50% 50%;
  }
`
