import React from 'react';

import { Column, Input } from '../styles/Additional';

function ComponentAdditionalLimit({ data, useExtra, setExtra }) {

  // eslint-disable-next-line no-unused-vars
  const [id, assoc] = data;
  const { limit = 0 } = assoc;

  const inputFn = (val) => {
    if (!isNaN(Number(val)) && Number(val) < 100 && val.replace(/[^0-9]/g, '') !== limit) {
      setExtra((prevState) => {
        const { ...rest } = prevState[id] || {};
        return {
          ...prevState,
          [id] : {
            ...rest,
            limit: parseInt(Number(val))
          }
        };
      });
    };
  };

  return (
    <Column width="20%">
      <Input type="text" placeholder="Limite"
        onInput={(e) => inputFn(e.target.value)} autoCapitalize="on" autoCorrect="off"
        onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
        onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off"
        value={limit || ''} />
    </Column>
  );
}

export default ComponentAdditionalLimit;
