import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, usePick, setPick }) {

  return (
    <ElementContext.Provider value={{ usePick, setPick }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotPick() {
  const context = useContext(ElementContext)
  const { usePick, setPick } = context;
  return { usePick, setPick };
}
