import React from 'react';
import { useAsync } from 'react-async';

import { getImageUrl } from '../app/';

import { storage } from '../services/server';
import { SnapshotWhere } from '../contexts/App';

import ComponentProductsThumb from './ComponentProductsThumb';
import ComponentSkeletonProductsThumb from './ComponentSkeletonProductsThumb';
import ComponentProductThumbNull from './ComponentProductThumbNull';

const awaitStorage = async (where, data) => {

  const { name } = data;

  async function processFn(src){
    const img = new Image();
    img.src = src;
    await img.decode();
    return img.src
  }

  return await storage()
    .ref(getImageUrl({ where, ref: `${name}_160x160.jpeg` }))
    .getDownloadURL()
    .then(processFn)
};

const fetchPerson = async ({ useWhere, thumb }, { signal }) => {

  const [image] = thumb;
  return await awaitStorage(useWhere, image);
};

function ComponentAwaitProductThumb({ thumb }) {

  // eslint-disable-next-line no-unused-vars
  const { useWhere, setWhere } = SnapshotWhere();
  const { data, error } = useAsync({ promiseFn: fetchPerson, useWhere, thumb });

  if (error) return <ComponentProductThumbNull />
  if (data) return <ComponentProductsThumb {...{ data }} />
  return <ComponentSkeletonProductsThumb />
};

export default ComponentAwaitProductThumb;
