import React from 'react';

import Bracael from '../Bracael';

import ComponentComandaExtra from './ComponentComandaExtra';
import ComponentComandaSelector from './ComponentComandaSelector';
import ComponentComandaSelectorUndefined from './ComponentComandaSelectorUndefined';
import ComponentComandaExtraUndefined from './ComponentComandaExtraUndefined';

import { Li, Hash, Name, QTD, Price } from '../styles/Comanda';

function ComponentMapDetail({ assoc, i, feed }) {

  const { id, amount = 1, additional = {}, selector = {}, comment = '' } = assoc;
  const { title, price, weight, grams, extra = {}, pick = {}, price_off = 0 } = Object.entries(feed).find((data) => data[0] === id)[1];

  // const unitySize = weight
  //   ? Bracael.unitSize({ weight, space: true })
  //   : Bracael.unitSize({ grams, space: true });

  return (
    <>
      <Li>
        <Hash>{new Array(3 + 1 - (i + '').length).join('0') + ++i}</Hash>
        <Name>{title} {weight ? Bracael.unitSize({ ml: weight }) : grams ? Bracael.unitSize({ gm: grams }) : ''}</Name>
        <QTD>{amount}un</QTD>
        <Price>{Number(amount * (price - price * price_off / 100)).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</Price>
      </Li>
      {Object.keys(selector).length
        ? Object.entries(selector).map((assoc) => pick[assoc[0]]
          ? <ComponentComandaSelector key={assoc[0]} {...{ assoc, pick }} />
          : <ComponentComandaSelectorUndefined key={assoc[0]} />)
        : null}
      {Object.keys(additional).length
        ? Object.entries(additional).map((assoc) => extra[assoc[0]]
          ? <ComponentComandaExtra key={assoc[0]} {...{ assoc, extra }} />
          : <ComponentComandaExtraUndefined key={assoc[0]} {...{ assoc }} />)
        : null}
      {comment
      ? (<Li liColor="#0300cc">
          <Hash>--</Hash>
          <Name>{comment}</Name>
          <QTD>--</QTD>
          <Price>--</Price>
        </Li>)
      : null}
    </>
  )
}

export default ComponentMapDetail;
