import React, { useEffect } from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Modal, Content } from '../styles/Modal';
import { Main, Title, MSG, Footer, Simple, Button } from '../styles/ModalGallery';

const { jQuery: $ } = window;

function ComponentModalExtension({ useModal, setModal, statusRef, modalRef }) {

  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();
  const { name } = useModal;

  useEffect(() => {
    if (statusRef.current && name) {
      $(modalRef.current).fadeIn(200);
    };
  }, [statusRef, modalRef, name]);

  const cancenlFn = () => {
    setModal({});
  };

  const removeFn = () => {
    setModal({});
    setPost((prevState) => {
      const assoc = {...prevState};
      delete assoc[name];
      return assoc;
    })
  };

  return (
    <Modal ref={modalRef}>
      <Content>
        <Main>
          <Title>Excluir extensão</Title>
          <MSG>Só confirmando: deseja mesmo Excluir este seletor?</MSG>
          <Footer>
            <Simple type="button" onClick={() => cancenlFn()}>Cancelar</Simple>
            <Button type="button" onClick={() => removeFn()}>Excluir</Button>
          </Footer>
        </Main>
      </Content>
    </Modal>
  );
}

export default ComponentModalExtension;
