import React, { useEffect, useRef } from 'react';

import { SnapshotDelete } from '../contexts/IMG';

import ComponentModalThumbMain from './ComponentModalThumbMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalThumb() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useDelete, setDelete } = SnapshotDelete();
  const { name } = useDelete;

  useEffect(() => {
    const { status } = useDelete;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useDelete]);

  useEffect(() => {
    const { status } = useDelete;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setDelete({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useDelete]);

  const cancenlFn = () => setDelete((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} backgroundColor="rgba(0,0,0,0.5)" zIndex="6">
      {(name || useDelete.status === false) ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4" alignItems="center">
          <ComponentModalThumbMain close={cancenlFn} {...{ name }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalThumb;
