import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Group, Textarea } from '../styles/Post';

function ComponentEditDesciption() {

  const { usePost, setPost } = SnapshotPost();
  const { description } = usePost;

  const altDescription = val => setPost((prevState) => Object({
    ...prevState,
    description: val.replace(/\n/gi, "\n")
  }));

  return (
    <Group hasText={true} textarea>
      <Textarea maxLength="600" placeholder="Descrição" onInput={(e) => altDescription(e.target.value)}
        autoCapitalize="on" autoCorrect="off" onDrag={(e) => e.preventDefault()}
        onDrop={(e) => e.preventDefault()} autoComplete="off" value={description}></Textarea>
    </Group>
  );
};

export default ComponentEditDesciption;
