import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import ComponentProductCategory from './ComponentProductCategory';
import ComponentProductScreenEdit from '../components/ComponentProductScreenEdit';

function ComponentProductPost() {

  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();
  const { category } = usePost;

  if (!category) return <ComponentProductCategory />
  return <ComponentProductScreenEdit />
}

export default ComponentProductPost;
