import React from 'react';

import { SnapshotDelete, SnapshotRef } from '../contexts/Gallery';

import { Li, IMG } from '../styles/Gallery';

function ComponentGalleryDelete({ data }) {

  const { clickRef } = SnapshotRef();
  const { setDelete } = SnapshotDelete();
  const { assoc, url } = data;
  const { name } = assoc;

  const selectFn = () => {
    if (clickRef.current) {
      clickRef.current = false;
      setDelete({
        name: name.split('_160x160.jpeg')[0],
        status: true
      });
    };
  };

  return (
    <Li status={false}>
      <IMG style={{ backgroundImage: "url(" + url + ")" }} onClick={() => selectFn()}>
        <img src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw==" height="100%" width="100%" alt="" />
      </IMG>
    </Li>
  );
}

export default ComponentGalleryDelete;
