import React, { useEffect, useRef } from 'react';
import ReactDOM from 'react-dom';
import { useHistory } from 'react-router-dom';

import { auth } from '../services/server';
import { SnapshotOverlay, SnapshotBusiness } from '../contexts/App';

import ComponentModalLeave from './ComponentModalLeave';

import { Content, Title, Commerce, Nav } from '../styles/Hidden';

function ComponentSidebar() {

  const history = useHistory();
  const statusRef = useRef(false);
  const { root } = SnapshotBusiness();
  const { useOverlay, setOverlay } = SnapshotOverlay();
  const { brand = '', commerce = '', invoice = '', onwer = '' } = root;

  useEffect(() => {
    if (statusRef.current) {
      if (useOverlay) {
        document.querySelector('#root').style.transform = "translate3d(-256px, 0, 0)";
      }
      else {
        document.querySelector('#root').style.transform = null;
      };
    }
    else {
      statusRef.current = true;
    }
  }, [useOverlay]);


  const useComponent = (() => {
    const Root = document.createElement('div')
    Root.setAttribute('role', 'modal');
    return Root
  })()

  const leaveFn = () => {
    const data = {
      title: 'Sair da conta?',
      message: 'Confirme para sair da conta.',
      button: ['Cancelar', 'Confirmar'],
      buttonColor: '#ff2222',
      action: function () {
        setOverlay(prevState => !prevState)
        history.push('/')
        auth().signOut()
      }
    }
    RenderComponent(data)
  }

  const RenderComponent = data => {
    ReactDOM.render(<ComponentModalLeave {...data} />, document.body.appendChild(useComponent))
  }

  return (
    <Content>
      <Title>
        <span>Ferramentas</span>
      </Title>
      <div>
        <Commerce>
          <li>
            <span>Razão social</span>
            <p>{brand}</p>
          </li>
          <li>
            <span>Comércio</span>
            <p>{commerce}</p>
          </li>
          <li>
            <span>Status</span>
            <p>{invoice ? 'Ativo' : 'Pendente'}</p>
          </li>
          <li>
            <span>Em nome de</span>
            <p>{onwer}</p>
          </li>
        </Commerce>
      </div>
      <div>
        <Nav>
          <button type="button" onClick={() => alert('Página em desenvolvimento...')}>Pedidos arquivados</button>
          <button type="button" onClick={() => alert('Página em desenvolvimento...')}>Pedidos excluídos</button>
        </Nav>
      </div>
      <div>
        <small>Controle as configurações do seu comércio e gerencie todos os pedidos recebidos até a entrega para o cliente com uma excelente experiência.</small>
      </div>
      <div>
        <button type="text" onClick={() => leaveFn()}>
          <svg xmlns="http://www.w3.org/2000/svg" height="16" viewBox="0 0 515.556 515.556" width="16"><path d="m225.556 0h64.444v257.778h-64.444z" /><path d="m322.222 73.944v68.602c56.798 24.932 96.667 81.559 96.667 147.454 0 88.832-72.28 161.111-161.111 161.111s-161.111-72.279-161.111-161.111c0-65.896 39.869-122.523 96.667-147.454v-68.602c-93.051 27.813-161.112 114.097-161.112 216.056 0 124.358 101.182 225.556 225.556 225.556s225.556-101.198 225.556-225.556c0-101.959-68.061-188.243-161.112-216.056z" /></svg>
          <span>Sair</span>
        </button>
      </div>
    </Content>
  );
}

export default ComponentSidebar;
