import React, { useState, useRef } from 'react';

import { SnapshotDelivery } from '../contexts/Settings';

import ComponentMapFrete from './ComponentMapFrete';
import ComponentFreteForm from './ComponentFreteForm';
import ComponentDeliveryDelete from './ComponentDeliveryDelete';

import { Main, Small } from '../styles/Settings';

function ComponentSettingsFrete() {

  const [useDelete, setDelete] = useState({});
  const organize = (a, b) => (a[1].district > b[1].district) ? 1 : ((b[1].district > a[1].district) ? -1 : 0);

  const contentRef = useRef(null);
  const { useDelivery } = SnapshotDelivery();

  return (
    <>
      <Main ref={contentRef} key="delivery">
        <ComponentFreteForm />
        {Object.keys(useDelivery || {})
        ? Object.entries(useDelivery)
          .sort(organize)
          .map((data) => <ComponentMapFrete key={data[1].moment || data[0]} {...{ data, useDelete, setDelete }} />)
        : <Small>Não há nada por enquanto.</Small>}
      </Main>
      <ComponentDeliveryDelete {...{ useDelete, setDelete }} />
    </>
  );
}

export default ComponentSettingsFrete;
