import React from 'react';

import { SnapshotPost } from '../contexts/ProductId';

import { Main, Title, MSG, Footer, Simple, Button } from '../styles/ModalGallery';

function ComponentModalThumbMain({ close, name }) {

  const { usePost, setPost } = SnapshotPost();

  const removeFn = () => {
    const assoc = {...usePost};
    assoc.thumb.splice(assoc.thumb.map((data) => data.name).indexOf(name), 1);
    setPost(assoc);
    close();
  };

  return (
    <Main marginBottom="initial">
      <Title>Excluir imagem</Title>
      <MSG>Ei! Só confirmando, deseja mesmo remover esta imagem?</MSG>
      <Footer>
        <Simple type="button" onClick={() => close()}>Cancelar</Simple>
        <Button type="button" onClick={() => removeFn()}>Excluir</Button>
      </Footer>
    </Main>
  );
}

export default ComponentModalThumbMain;
