import React from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { SnapshotPost } from '../contexts/ProductId';
import { SnapshotAdd, SnapshotRef } from '../contexts/Gallery';

import { Li, IMG } from '../styles/Gallery';

function ComponentGallerySelect({ data }) {

  const { clickRef } = SnapshotRef();
  const { setAdd } = SnapshotAdd();
  const { usePost } = SnapshotPost();
  const { thumb = [] } = usePost;
  const { assoc, url } = data;
  const { name } = assoc;

  const selectFn = () => {
    if (clickRef.current) {
      if (thumb.length <= 8) {
        if (!thumb.map((data) => data.name).includes(name.split('_160x160.jpeg')[0])) {
          clickRef.current = false;
          setAdd({
            status: true,
            name: name.split('_160x160.jpeg')[0]
          });
        };
      }
      else {
        toast.info('Maxímo de 8 imagens!', Bracael.toast_options);
      };
    };
  };

  return (
    <Li status={thumb.map((data) => data.name).includes(name.split('_160x160.jpeg')[0])}>
      <IMG style={{ backgroundImage: "url(" + url + ")" }} onClick={() => selectFn()}>
        <img src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEAAAEALAAAAAABAAEAgM7t3M7t3AICTAEAOw==" height="100%" width="100%" alt="" />
      </IMG>
    </Li>
  );
}

export default ComponentGallerySelect;
