import React, { useEffect, useRef, useState } from 'react';

import ComponentHeader from '../components/ComponentHeader';
import ComponentAwaitService from '../components/ComponentAwaitService';
import ComponentOverload from '../components/ComponentOverload';

import { Mainly } from '../styles/root';

const { jQuery: $ } = window;

function Service() {

  const configGlobal = {
    name_content: "Área de serviço"
  };

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [useModal, setModal] = useState({});

  useEffect(() => {
    const { status } = useModal;
    if (statusRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setModal({}));
    }
    else {
      statusRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useModal]);

  return (
    <>
      <ComponentHeader {...{ configGlobal }} />
      <Mainly>
        <ComponentAwaitService {...{ useModal, setModal }} />
        <ComponentOverload {...{ useModal, setModal, modalRef }} />
      </Mainly>
    </>
  );
};

export default Service;
