import React, { useState, useEffect, useRef } from 'react';
import { useLocation, useHistory } from 'react-router-dom';

import { database } from '../services/server';
import { SnapshotWhere } from '../contexts/App';
import ElementProvider from '../contexts/Settings';

import ComponentSettingsBreadcrumb from './ComponentSettingsBreadcrumb';
import ComponentSettingsSection from './ComponentSettingsSection';

import { Content } from '../styles/Settings';

function ComponentSettings({ data, useModal, setModal }) {

  const history = useHistory();
  const { search } = useLocation();
  const { outside = {}, cupons = {}, delivery = {} } = data;

  const businessRef = useRef(null);
  const onBusinessRef = useRef(false);

  const deliveryRef = useRef(null);
  const onDeliveryRef = useRef(false);

  const cuponsRef = useRef(null);
  const onCuponsRef = useRef(false);

  const contentRef = useRef(null);

  const routeRef = useRef(null);
  const [useOutside, setOutside] = useState(outside);
  const [useCupons, setCupons] = useState(cupons);
  const [useDelivery, setDelivery] = useState(delivery);
  const [useSection, setSection] = useState(-1);
  const { useWhere } = SnapshotWhere();

  useEffect(() => {
    const query = new URLSearchParams(search);
    const paramSection = query.get('section');

    if (routeRef.current) {
      if (paramSection === 'accessibility') {
        setSection(6);
      }
      else if (paramSection === 'cupom') {
        setSection(7);
      }
      else if (paramSection === 'cupons') {
        setSection(8);
      }
      else {
        setSection(-1);
      }
    }
    else {
      routeRef.current = true;
    };
  }, [search]);

  useEffect(() => {
    if (search !== '') {
      history.push('/settings');
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    businessRef.current = database()
    .ref(`${useWhere}/business/outside`)
    .on('value', snapshot => {
      if (contentRef.current) {
        if (onBusinessRef.current) {
          setOutside(snapshot.val() || {});
        }
        else {
          onBusinessRef.current = true;
        };
      }
      else {
        businessRef.current = null;
      };
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [businessRef]);

  useEffect(() => {
    deliveryRef.current = database()
    .ref(`${useWhere}/business/delivery`)
    .on('value', snapshot => {
      if (contentRef.current) {
        if (onCuponsRef.current) {
          setDelivery(snapshot.val() || {});
        }
        else {
          onCuponsRef.current = true;
        };
      }
      else {
        delivery.current = null;
      };
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deliveryRef]);

  useEffect(() => {
    cuponsRef.current = database()
    .ref(`${useWhere}/business/cupons`)
    .on('value', snapshot => {
      if (contentRef.current) {
        if (onDeliveryRef.current) {
          setCupons(snapshot.val() || {});
        }
        else {
          onDeliveryRef.current = true;
        };
      }
      else {
        delivery.current = null;
      };
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cuponsRef]);

  return (
    <>
      <ComponentSettingsBreadcrumb {...{ useSection, setSection }} />
      <Content ref={contentRef}>
        <ElementProvider {...{ useModal, setModal, useSection, setSection, useOutside, setOutside, useCupons, setCupons, useDelivery, setDelivery }}>
          <ComponentSettingsSection />
        </ElementProvider>
      </Content>
    </>
  );
}

export default ComponentSettings;
