import React, { useEffect, useRef } from 'react';

import { SnapshotRef, SnapshotAdd } from '../contexts/Gallery';

import ComponentModalAddIMGMain from './ComponentModalAddIMGMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentModalAddIMG() {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { clickRef } = SnapshotRef();
  const { useAdd, setAdd } = SnapshotAdd();
  const { name } = useAdd;

  useEffect(() => {
    const { status } = useAdd;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useAdd]);

  useEffect(() => {
    const { status } = useAdd;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => {
        clickRef.current = true;
        setAdd({});
      });
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useAdd]);

  const cancenlFn = () => setAdd((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef}>
      {(name || useAdd.status === false) ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentModalAddIMGMain close={cancenlFn} {...{ name }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentModalAddIMG;
