import React, { createContext, useContext } from 'react';

const ElementContext = createContext();

export default function ElementProvider({ children, useList, setList, useFilter, setFilter, useBar, setBar, useUnvOn, setUnvOn, useUnvOff, setUnvOff, useDraftOn, setDraftOn, useDraftOff, setDraftOff, useDelete, setDelete, dataRef }) {

  return (
    <ElementContext.Provider value={{ useList, setList, useFilter, setFilter, useBar, setBar, useUnvOn, setUnvOn, useUnvOff, setUnvOff, useDraftOn, setDraftOn, useDraftOff, setDraftOff, useDelete, setDelete, dataRef }}>
      {children}
    </ElementContext.Provider>
  );
}

export function SnapshotList() {
  const context = useContext(ElementContext)
  const { useList, setList } = context;
  return { useList, setList };
}

export function SnapshotFilter() {
  const context = useContext(ElementContext)
  const { useFilter, setFilter } = context;
  return { useFilter, setFilter };
}

export function SnapshotBar() {
  const context = useContext(ElementContext)
  const { useBar, setBar } = context;
  return { useBar, setBar };
}

export function SnapshotUnvOn() {
  const context = useContext(ElementContext)
  const { useUnvOn, setUnvOn } = context;
  return { useUnvOn, setUnvOn };
}

export function SnapshotUnvOff() {
  const context = useContext(ElementContext)
  const { useUnvOff, setUnvOff } = context;
  return { useUnvOff, setUnvOff };
}

export function SnapshotDraftOn() {
  const context = useContext(ElementContext)
  const { useDraftOn, setDraftOn } = context;
  return { useDraftOn, setDraftOn };
}

export function SnapshotDraftOff() {
  const context = useContext(ElementContext)
  const { useDraftOff, setDraftOff } = context;
  return { useDraftOff, setDraftOff };
}

export function SnapshotDelete() {
  const context = useContext(ElementContext)
  const { useDelete, setDelete } = context;
  return { useDelete, setDelete };
}

export function SnapshotData() {
  const context = useContext(ElementContext)
  const { dataRef } = context;
  return { data: dataRef.current };
}
