import React, { useEffect, useRef } from 'react';

import { Modal, Content } from '../styles/Modal';
import { Main, B1, B2, B3 } from '../styles/Overload';

const { jQuery: $ } = window;

function ComponentOverload({ useModal, setModal, modalRef }) {

  const statusRef = useRef(false);

  useEffect(() => {
    const { status } = useModal;
    if (statusRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusRef.current = true;
    };
  }, [statusRef, modalRef, useModal]);

  return (
    <Modal ref={modalRef} zIndex="10">
      <Content>
        <Main>
          <B1 />
          <B2 />
          <B3 />
        </Main>
      </Content>
    </Modal>
  );
}

export default ComponentOverload;
