import React, { useEffect, useRef, useState } from 'react';

import { SnapshotScreen, SnapshotPost } from '../contexts/ProductId';

import ComponentExtensionButtons from './ComponentExtensionButtons';
import ComponentModalExtension from './ComponentModalExtension';

import { Content, Head, Title, Button, Main, Small } from '../styles/Extension';

const { jQuery: $ } = window;

function ComponentEditExtension() {

  const statusRef = useRef(false);
  const modalRef = useRef(null);
  const [useModal, setModal] = useState({});

  // eslint-disable-next-line no-unused-vars
  const { useScreen, setScreen } = SnapshotScreen();
  // eslint-disable-next-line no-unused-vars
  const { usePost, setPost } = SnapshotPost();
  const { extra = {}, pick = {} } = usePost;

  useEffect(() => {
    const { status, name } = useModal;
    if (statusRef.current) {
      if (!status && !name) {
        $(modalRef.current).fadeOut(300);
      };
    }
    else {
      statusRef.current = true;
    };
  }, [useModal]);

  return (
    <Content>
      <Head>
        <Title>Extensões</Title>
        <Button type="button" onClick={() => setScreen(3)}>
          <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 515.556 515.556"><path d="m257.778 0c-142.137 0-257.778 115.641-257.778 257.778s115.641 257.778 257.778 257.778 257.778-115.641 257.778-257.778-115.642-257.778-257.778-257.778zm128.889 290h-96.667v96.667h-64.444v-96.667h-96.667v-64.444h96.667v-96.667h64.444v96.667h96.667z" /></svg>
        </Button>
      </Head>
      <Main>
        {!!(Object.keys(extra).length + Object.keys(pick).length)
        ? <ComponentExtensionButtons {...{ useModal, setModal }} />
        : <Small>Não há extensões adicionadas a este produto. Gostaria de adicionar alguma?</Small>}
      </Main>
      <ComponentModalExtension {...{ useModal, setModal, statusRef, modalRef }} />
    </Content>
  );
}

export default ComponentEditExtension;
