import React, { useRef, useState } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Mainly, Section, Name, Status, Label } from '../styles/Modify';

function ComponentPaymentVoucher() {

  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const { alelo = false, sodexo = false, ticket = false, vrref = false } = useOutside.profile.methods.voucher || {};

  const labelFn = (val, data) => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: `profile/methods/voucher/${val}`, data })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (!status) {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  };

  return (
    <Content>
      <Name>Voucher</Name>
      <Mainly>
        <Section>
          <Status type="checkbox" readOnly checked={alelo} />
          <Label onClick={() => labelFn('alelo', !alelo)}>ALELO</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={sodexo} />
          <Label onClick={() => labelFn('sodexo', !sodexo)}>SODEXO</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={ticket} />
          <Label onClick={() => labelFn('ticket', !ticket)}>TICKET</Label>
        </Section>
        <Section>
          <Status type="checkbox" readOnly checked={vrref} />
          <Label onClick={() => labelFn('vrref', !vrref)}>VR</Label>
        </Section>
      </Mainly>
    </Content>
  );
}

export default ComponentPaymentVoucher;
