/* eslint-disable no-unused-vars */
import React, { useState, useRef } from 'react';
import { toast } from 'react-toastify';

import Bracael from '../Bracael';
import { functions } from '../services/server';
import { SnapshotToken } from '../contexts/App';
import { SnapshotModal, SnapshotOutside } from '../contexts/Settings';

import { Content, Name, Main, Section, Under, Form, Subname, HREF, Footer, Simple, Button } from '../styles/Modify';
import { Clock, Input, MSG } from '../styles/Clock';

function ComponentMapLive({ data, i, useShow, setShow }) {

  const clickRef = useRef(true);
  const [useAttempts, setAttempts] = useState(0);
  const { token } = SnapshotToken();
  const { setModal } = SnapshotModal();
  const { useOutside } = SnapshotOutside();
  const [useClock, setClock] = useState(JSON.stringify(useOutside.profile.streaming[data] || [["", ""]]));

  const isValid = (cache) => {

    return cache.length < (useOutside.profile.streaming[data] || [["", ""]]).length
    ? cache.every((data) => !!data[0] && !!data[1])
      ? (useOutside.profile.streaming[data] || [["", ""]]).map((assoc, i) => {
        const [a, b] = assoc;
        return cache[i]
        ? a === cache[i][0]
          && b === cache[i][1]
        : false
    }).includes(false) : false
    : cache.every((data) => !!data[0] && !!data[1])
      ? cache.map((assoc, i) => {
        const [a, b] = assoc;
        return useOutside.profile.streaming[data][i]
        ? a === useOutside.profile.streaming[data][i][0]
          && b === useOutside.profile.streaming[data][i][1]
        : false
      }).includes(false): false;
  };

  const inputFn = (val, i, ii) => {
    const assoc = [...JSON.parse(useClock)];
    assoc[ii][i] = val;
    setClock(JSON.stringify(assoc));
  };

  const removeFn = (val) => {
    const assoc = [...JSON.parse(useClock)];
    assoc.splice(val, 1);
    setClock(JSON.stringify(assoc));
  };

  const newFn = () => {
    const assoc = [...JSON.parse(useClock)];
    if (assoc.every((data) => !!(data[0] && data[1]))) {
      assoc.push(["", ""]);
      setClock(JSON.stringify(assoc));
    }
    else {
      toast('Termine o item atual', Bracael.toast_options);
    };
  };

  const cancelFn = () => {
    setShow(-1);
    if (isValid(JSON.parse(useClock))) {
      setClock(JSON.stringify(useOutside.profile.streaming[data] || [["", ""]]));
    };
  };

  const saveFn = () => {
    if(clickRef.current) {
      clickRef.current = false;
      setModal({ status: true });

      functions()
      .httpsCallable('setBusiness')({ token, ref: `profile/streaming/${data}`, data: JSON.parse(useClock) })
      .then((data) => data.data)
      .then(function(data) {
        const { status } = data;
        if (status) {
          toast('Configurações salvas!', Bracael.toast_options);
          setShow(-1);
        }
        else {
          if (useAttempts < 3) {
            toast.error('Algo saiu mal, tente novamente!', Bracael.toast_options);
            setAttempts((prevState) => prevState+1);
          }
          else {
            toast.error('Se este erro persistir, contate o fornecedor!', Bracael.toast_options);
          };
        };
      })
      .catch(function() {
        toast.error('Erro na chamada API', Bracael.toast_options);
      })
      .finally(function() {
        clickRef.current = true;
        setModal({ status: false });
      });
    };
  }

  return (
    <Content>
      <Name>{Bracael.u.b[i]}</Name>
      <Main status={useShow === i}>
        <Clock>
          {(useOutside.profile.streaming[data] || [["", ""]])
          .map((assoc, ii) => {
            const [a, b] = assoc;
            return (
              <Section key={ii}>
                {(a && b)
                ? <Subname>{a} ÁS {b}</Subname>
                : <Subname style={{ color: "#ff6b6b" }}>Fechado</Subname>}
                {ii === 0 ? <HREF type="button" onClick={() => setShow(i)}>Editar</HREF> : null}
              </Section>
            );
          })}
        </Clock>
        <Under>
          <Form>
            {JSON.parse(useClock)
            .map((assoc, ii) => {
              const [a, b] = assoc;
              return (
                <Section key={`${i}-${ii}`}>
                  <Input type="time" onChange={(e) => inputFn(e.target.value, 0, ii)} value={a} />
                  <span>às</span>
                  <Input type="time" onChange={(e) => inputFn(e.target.value, 1, ii)} value={b} />
                  {ii !== 0 ? <HREF type="button" onClick={() => removeFn(ii)}>Excluir</HREF> : null}
                </Section>
              );
            })}
          </Form>
          <HREF type="button" onClick={() => newFn()}>Adicionar novo</HREF>
          <MSG>Para definir um dia como fechado mantenha o campo em branco.</MSG>
          <Footer>
            <Button type="button" status={isValid(JSON.parse(useClock))} onClick={() => isValid(JSON.parse(useClock)) && saveFn()}>Salvar alterações</Button>
            <Simple type="button" onClick={() => cancelFn()}>Cancelar</Simple>
          </Footer>
        </Under>
      </Main>
    </Content>
  );
}

export default ComponentMapLive;
