import React, { useEffect, useRef, useState } from 'react';
import pt from 'date-fns/locale/pt-BR';
import { formatDistanceToNow } from 'date-fns';

import Bracael from '../Bracael';
import { SnapshotDecline, SnapshotAccept } from '../contexts/Service';

import ComponentMapDetail from './ComponentMapDetail';

import { Content, Controls, Simple, Button, Main, Client, Command } from '../styles/New';
import { List, Strong, Hash, Name, QTD, Price, Subtotal, Frete, Total } from '../styles/Comanda';

function ComponentServiceNew({ data, cupons, feed, delivery }) {

  const swipeRef = useRef(null);
  const timestampRef = useRef(null);
  const intervalRef = useRef(null);
  const { setAccept } = SnapshotAccept();
  const { setDecline } = SnapshotDecline();
  const [id, assoc] = data;
  // eslint-disable-next-line no-unused-vars
  const { display_name, house_number, complement = '', payment, delivery_select, street, telephone_number, timestamp, trade, detail = [], order_type, cupom_promotion } = assoc;
  const { district, rate = 0, zone } = order_type !== 2 ? delivery[delivery_select] : {};
  const { type_off = '001', value = 0 } = cupons[cupom_promotion] || {};

  const totalCart = type_off === '001'
  ? parseFloat((Bracael.subtotal({ detail, feed }) - Bracael.subtotal({ detail, feed }) * value / 100).toFixed(2))
  : parseFloat((Bracael.subtotal({ detail, feed }) + ~value+1).toFixed(2));

  const acceptFn = () => setAccept({ id, status: true });
  const declineFn = () => setDecline({ id, status: true });

  const formatMethods = () => {
    const { cash = false, pix = false, debit = [], credit = [], voucher = [] } = payment || {};
    const user_method = [];
    (cash || false) && user_method.push('Dinheiro');
    (pix || false) && user_method.push('Pix');
    (debit || []).length && user_method.push('Débito');
    (credit || []).length && user_method.push('Crédito');
    (voucher || []).length && user_method.push('Voucher');

    return `${user_method.join(', ').replace(/, ([^,]*)$/, ' e $1')}`;
  };

  const [useSwipeable, setSwipeable] = useState({
    isDown: false,
  });

  // eslint-disable-next-line no-unused-vars
  const { isDown, startX, scrollLeft, active } = useSwipeable;

  const MouseDown = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      isDown: true,
      active: true,
      scrollLeft: swipeRef.current.scrollLeft,
      startX: event.pageX - swipeRef.current.offsetLeft
    }));

    if (!active) {
      swipeRef.current.classList.add("active");
    };
  };

  const MouseLeave = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      active: false,
      isDown: false
    }));

    if (active) {
      swipeRef.current.classList.remove("active");
    };
  };

  const MouseUp = (event) => {
    setSwipeable((prevState) => Object({
      ...prevState,
      active: false,
      isDown: false
    }));

    if (active) {
      swipeRef.current.classList.remove("active");
    };
  };

  const MouseMove = (event) => {
    const x = event.pageX - swipeRef.current.offsetLeft;
    const walk = (x - startX) * 1.25;

    event.preventDefault();

    if (!isDown) return; // para a function

    if (isDown) {
      swipeRef.current.scrollLeft = scrollLeft - walk;
    };
  };

  useEffect(() => {
    intervalRef.current = setInterval(() => {
      if (timestampRef.current) {
        if (formatDistanceToNow(new Date(timestamp), { addSuffix: true, locale: pt, timeZone: 'America/Sao_Paulo' }) !== timestampRef.current.innerText) {
          timestampRef.current.innerText = formatDistanceToNow(new Date(timestamp), { addSuffix: true, locale: pt, timeZone: 'America/Sao_Paulo' });
        };
      }
      else {
        clearInterval(intervalRef.current);
      };
    }, 1000);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [timestampRef]);

  return (
    <Content>
      <Controls>
        <Button type="button" onClick={() => acceptFn()}>Aceitar pedido</Button>
        <Simple type="button" onClick={() => declineFn()}>Recusar</Simple>
      </Controls>
      <Main draggable="true" ref={swipeRef}
      onMouseDown={MouseDown}
      onMouseLeave={MouseLeave}
      onMouseUp={MouseUp}
      onMouseMove={MouseMove}>
        <Client>
          <div>
            <div>
              <span>{display_name}</span>
              {order_type !== 2
              ? <p>{street}, {house_number}{complement ? ` - ${complement}` : ''}<br />{zone}{district ? ` - ${district}` : ''}</p>
              : <p>Retirada no local</p>}
            </div>
            <div>
              <span>Pagamento: {formatMethods()}</span>
              {trade ?
              <span>Troco para: {Number(trade).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
              : null}
            </div>
            <div>
              <span ref={timestampRef}>{formatDistanceToNow(new Date(timestamp), { addSuffix: true, locale: pt, timeZone: 'America/Sao_Paulo' })}</span>
            </div>
          </div>
        </Client>
        <Command>
          <Strong>
            <Hash>#</Hash>
            <Name>DESCRIÇÃO</Name>
            <QTD>QTD.</QTD>
            <Price>TOTAL</Price>
          </Strong>
          <List>
            {detail
            .map((assoc, i) => <ComponentMapDetail key={assoc.moment} {...{ assoc, i, feed }} />)}
          </List>
          <Subtotal>
            <span>SUBTOTAL</span>
            <span>{Number(Bracael.subtotal({ detail, feed })).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Subtotal>
          {order_type !== 2
          &&<Frete>
            <span><small>ACRESCIMO</small>VALOR DA ENTREGA</span>
            <span>{Number(rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Frete>}
          {(!value && !!cupom_promotion)
          ? <Frete>
              <span><small>DESCONTO</small>CUPOM INDEFINIDO</span>
              <span>Oferta não encontrada</span>
            </Frete>
          : !!value && <Frete>
            <span><small>DESCONTO</small>CUPOM {value}% OFF</span>
            <span>{type_off !== '001'
              ? Number(~value+1).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })
              : Number(-Math.abs(Bracael.subtotal({ detail, feed }) - totalCart)).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Frete>}
          <Total>
            <b>TOTAL A PAGAR</b>
            <span>{Number(totalCart + rate).toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}</span>
          </Total>
        </Command>
      </Main>
    </Content>
  );
}

export default ComponentServiceNew;
