import React from 'react';

import { Modal, Close, Content, Title, Footer, Message } from '../styles/Leave';

const { jQuery: $ } = window;

function ComponentModalLeave({ title, message, button, buttonColor, onClick = true, action }) {

  const cancenlFn = () => $('[role="modal"]').fadeOut(300, function () { this.remove(); });

  const confirmFn = () => {
    action();
    cancenlFn();
  };

  $('[role="modal"]').fadeIn(200);

  return (
    <Modal component={onClick}>
      {onClick && <Close onClick={() => cancenlFn()} />}
      <Content>
        <Title>
          <span>{title}</span>
        </Title>
        <Message>{message}</Message>
        <Footer buttonColor={buttonColor && buttonColor}>
            <button type="button" tabIndex="0" onClick={() => cancenlFn()}>{button[0]}</button>
            <button type="button" tabIndex="0" onClick={() => confirmFn()}>{button[1]}</button>
        </Footer>
      </Content>
    </Modal>
  );
}

export default ComponentModalLeave;
