import React, { createContext, useContext } from 'react';

const ComponentContext = createContext();

export default function ComponentProvider({ children, useToggle, setToggle, useInputEmail, setInputEmail, useInputPassword, setInputPassword }) {

  return (
    <ComponentContext.Provider value={{ useToggle, setToggle, useInputEmail, setInputEmail, useInputPassword, setInputPassword }}>
      {children}
    </ComponentContext.Provider>
  );
}

export function SnapshotToggle() {
  const context = useContext(ComponentContext)
  const { useToggle, setToggle } = context;
  return { useToggle, setToggle };
}
