import React from 'react';

import Bracael from '../Bracael';
import { SnapshotBusiness } from '../contexts/App';

import { Status, Input, Label } from '../styles/Week';

function ComponentMapWeek({ data, i, useDaily, setDaily }) {

  const { profile } = SnapshotBusiness();
  const { streaming } = profile;

  const disable_this = streaming[data].map(([start, end]) => !!start && !!end).includes(false);

  const altInput = () => setDaily((prevState) => Object({
    ...prevState,
    [data]: !useDaily[data]
  }));

  return (
    <Status>
      <Input type="checkbox"
        readOnly
        disabled={disable_this}
        checked={!!useDaily[data]} />
      <Label onClick={() => !disable_this && altInput()}>{Bracael.u.b[Bracael.u.d.indexOf(data)]}</Label>
    </Status>
  );
}

export default ComponentMapWeek;
