import React from 'react';

import { Li, Hash, Name, QTD, Price } from '../styles/Comanda';

function ComponentComandaSelector({ assoc, pick }) {

  const uid = assoc[0];
  // const { amount } = assoc[1];
  // eslint-disable-next-line no-unused-vars
  const { name, limit, important } = pick[uid];

  return (
    <>
      <Li key={Math.random()} liColor="#c00">
        <Hash>--</Hash>
        <Name>{name}</Name>
        <QTD>--</QTD>
        <Price>--</Price>
      </Li>
      {assoc[1].map((data, i) => (
        <Li key={Math.random()}>
          <Hash>--</Hash>
          <Name>-- {important ? `${i+1} de ${limit} ` : ''}{data}</Name>
          <QTD>--</QTD>
          <Price>--</Price>
        </Li>
      ))}
    </>
  );
}

export default ComponentComandaSelector;
