import React, { useEffect, useRef, useState } from 'react';

import { Column, Input } from '../styles/Additional';

const { jQuery: $ } = window;

function ComponentAdditionalPrice({ data, useExtra, setExtra }) {

  const inputRef = useRef(null);
  // eslint-disable-next-line no-unused-vars
  const [id, assoc] = data;
  const { value } = assoc;
  const [useInput, setInput] = useState(value ? value.toFixed(2) : '');

  const inputFn = (val) => {
    setInput(val);
    setExtra((prevState) => {
      const { ...rest } = prevState[id] || {};
      return {
        ...prevState,
        [id] : {
          ...rest,
          value: isNaN(Number(val))
          ? Number(val.replace(/\./g, '').replace(/,/g, '.'))
          : Number(val)
        }
      };
    });
  };

  useEffect(() => {
    const { current } = inputRef;
    current.value = useInput;
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useInput]);

  useEffect(() => {
    const { current } = inputRef;
    $(current).mask('#.##0,00', { reverse: true });
  }, [inputRef]);

  return (
    <Column width="30%">
      <Input type="text" ref={inputRef} placeholder="0,00"
      onInput={(e) => inputFn(e.target.value)}
      autoCapitalize="off" autoCorrect="off"
      onPaste={(e) => e.preventDefault()} onCopy={(e) => e.preventDefault()}
      onCut={(e) => e.preventDefault()} onDrag={(e) => e.preventDefault()}
      onDrop={(e) => e.preventDefault()} autoComplete="off" />
    </Column>
  );
}

export default ComponentAdditionalPrice;
