import React from 'react';

import { SnapshotScreen } from '../contexts/ProductId';

import ComponentProductEdit from './ComponentProductEdit';
import ComponentProductGallery from './ComponentProductGallery';
import ComponentProductPlugins from './ComponentProductPlugins';
import ComponentErrorSmall from './ComponentErrorSmall';

function ComponentProductScreen() {

  const { useScreen } = SnapshotScreen();

  if (useScreen === 1) return <ComponentProductEdit />
  if (useScreen === 2) return <ComponentProductGallery />
  if (useScreen === 3) return <ComponentProductPlugins />
  return <ComponentErrorSmall />;
}

export default ComponentProductScreen;
