import React, { useEffect, useRef } from 'react';

import { SnapshotDelivery } from '../contexts/Settings';

import ComponentDeleteDeliveryMain from './ComponentDeleteDeliveryMain';

import { Modal, Close, Content } from '../styles/Modal';

const { jQuery: $ } = window;

function ComponentDeliveryDelete({ useDelete, setDelete }) {

  const modalRef = useRef(null);
  const statusonRef = useRef(false);
  const statusoffRef = useRef(false);
  const { useDelivery } = SnapshotDelivery();
  const { id } = useDelete;

  useEffect(() => {
    const { status } = useDelete;
    if (statusonRef.current && status) {
      $(modalRef.current).fadeIn(200);
    }
    else {
      statusonRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [statusonRef, modalRef, useDelete]);

  useEffect(() => {
    const { status } = useDelete;
    if (statusoffRef.current && status === false) {
      $(modalRef.current).fadeOut(300, () => setDelete({}));
    }
    else {
      statusoffRef.current = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [useDelete]);

  const cancenlFn = () => setDelete((prevState) => Object({ ...prevState, status: false }));

  return (
    <Modal ref={modalRef} zIndex="6">
      {useDelivery[id] ? (<>
        <Close onClick={() => cancenlFn()} />
        <Content zIndex="4">
          <ComponentDeleteDeliveryMain close={cancenlFn} {...{ id }} />
        </Content>
      </>) : null}
    </Modal>
  );
}

export default ComponentDeliveryDelete;
